const WALLET_BALANCE_DECIMALS = 4;
const ACTION_APY_DECIMALS = 4;

export const AssetPanelRow = observer<React.FunctionComponent<IProps>>(
  (props) => {
    const classes = useStyles();
    const commonClasses = useCommonClasses();
    const {
      marketType,
      assetSummary,
      // cTokenMeta,
      onCollateralableChange,
      onAssetSelected,
    } = props;

    const { address } = assetSummary;

    const {
      name,
      symbol,
      // amountInWallet,
      // isCollateralable,
      // supplyApy,
      // usdLiquidity,
    } = assetSummary;

    const cTokenStore = useCTokenStoreByBaseAsset(address);

    const isCollaterable = cTokenStore?.isCollaterable;

    // console.log({ supplyRatePerBlock: supplyRatePerBlock });

    const borrowApy = cTokenStore?.borrowApyInPercentages || 0;
    const supplyApy = cTokenStore?.supplyApyInPercentages || 0;

    const usdCashLiquidity = cTokenStore?.cTokenCashInUSD || 0;
    const walletBalanceInUnits =
      cTokenStore?.accountUnderlyingBalanceInUnits || 0;

    const liquidityUSDInScale = amountToSumInScale(usdCashLiquidity);

    const supplyInUnits = cTokenStore?.accountSupplyInUnits || 0;
    const borrowInUnits = cTokenStore?.accountBorrowInUnits || 0;

    const isSupply = marketType === "supply";
    const ApyToDisplay = isSupply ? supplyApy : borrowApy;
    const unitsToDisplay = isSupply ? supplyInUnits : borrowInUnits;
    const unitsText = isSupply ? "Supplied" : "Borrowed";
    const showActionUnits = unitsToDisplay > 0;

    const onCollateralSwitch = useCallback(
      (event: React.ChangeEvent<HTMLInputElement>) => {
        onCollateralableChange(address, event.target.checked);
      },
      [address, onCollateralableChange]
    );

    const onAssetInteractionRequested = useCallback(
      (event: React.MouseEvent<HTMLAnchorElement>) => {
        if (cTokenStore?.hasBeenInitialized) {
          onAssetSelected(address);
        } else {
          console.log(`Asset interation: cTokenStore not yet initialised`);
        }
      },
      [address, cTokenStore?.hasBeenInitialized, onAssetSelected]
    );

    const collateralLiquidityOrLoading = useMemo(() => {
      const collateralOrLiquidity =
        marketType === "supply" ? (
          // <input type="checkbox" />
          //  TODO : Add a proper toggle component
          <Switch
            checked={isCollaterable}
            onChange={onCollateralSwitch}
            // Preventing toggle-click from being intercepted by the row's 'onClick'
            onClick={(e) => e.stopPropagation()}
          />
        ) : (
          <div>
            $
            {parseFloat(
              liquidityUSDInScale.units.toLocaleString()
            ).toLocaleString()}
            {liquidityUSDInScale.scaleUnit}
          </div>
        );

      return cTokenStore?.hasBeenInitialized ? (
        collateralOrLiquidity
      ) : (
        <Skeleton animation="wave" />
      );
    }, [
      cTokenStore?.hasBeenInitialized,
      isCollaterable,
      liquidityUSDInScale.scaleUnit,
      liquidityUSDInScale.units,
      marketType,
      onCollateralSwitch,
    ]);

    return (
      <a
        className={clsx([classes.a, classes.asset])}
        onClick={onAssetInteractionRequested}
      >
        {/* Asset (+APY subtitle) */}
        <Grid container item xs={4} sm={4} className={classes.identity}>
          <TokenIcon
            symbol={symbol}
            extraClasses={[classes.identity_icon, classes.list_icon]}
          />
          <div className={clsx([classes.balance, classes.identity_balance])}>
            <div>{name}</div>
            <span
              className={clsx([
                commonClasses.mobileOnly,
                classes.balanceSubtitle,
              ])}
            >
              {ApyToDisplay}%
            </span>
          </div>
        </Grid>

        {/* APY (mobile hidden) */}
        <Grid
          item
          xs={1}
          sm={3}
          className={clsx([commonClasses.mobileHide, commonClasses.textCenter])}
        >
          <div className={clsx([classes.balance])}>
            {cTokenStore?.hasBeenInitialized ? (
              <div>
                {(+ApyToDisplay.toFixed(ACTION_APY_DECIMALS)).toLocaleString()}%
              </div>
            ) : (
              <Skeleton animation={"wave"} />
            )}
          </div>
        </Grid>

        {/* Wallet */}
        <Grid item xs={4} sm={3} className={clsx([commonClasses.textCenter])}>
          <div className={clsx([classes.balance])}>
            <div>
              {walletBalanceInUnits.toLocaleString()} {symbol}
            </div>
            {showActionUnits && (
              <span
                className={clsx([
                  // commonClasses.mobileOnly,
                  classes.balanceSubtitle,
                ])}
              >
                {(+unitsToDisplay.toFixed(
                  WALLET_BALANCE_DECIMALS
                )).toLocaleString()}{" "}
                {unitsText}
              </span>
            )}
          </div>
        </Grid>

        {/* Collateral / Liquidity */}
        <Grid item xs={4} sm={2} className={clsx([commonClasses.textRight])}>
          <div className={clsx([classes.balance])}>
            {collateralLiquidityOrLoading}
          </div>
        </Grid>
      </a>
    );
  }
);

type TNumericalScaleUnit = "M" | "K" | "";
type TSumInScale = {
  scaleUnit: TNumericalScaleUnit;
  units: number;
};

function amountToSumInScale(amount: number): TSumInScale {
  let scaleUnit: TNumericalScaleUnit = "M";
  let scaleDivider = 1_000_000;

  // TODO : Play with this threshold
  if (amount < 500_000) {
    scaleUnit = "K";
    scaleDivider = 1_000;
  }

  // For low numbers
  if (amount < 1000) {
    scaleUnit = "";
    scaleDivider = 1;
  }

  const sumInScale: TSumInScale = {
    scaleUnit,
    units: amount / scaleDivider,
  };

  return sumInScale;
}
