import { makeStyles } from "@material-ui/core";
import clsx from "clsx";
import React, { useRef } from "react";
import useHover from "@react-hook/hover";
import { AFFIRMATIVE_GREEN_COLOR } from "../../../../theme/themeConstants";
import Color from "color";

interface IProps {
  netApy: number;
  totalYearlyChangeUsd: number;
}

const useStyles = makeStyles((theme) => ({
  netApyWrapper: {
    // TODO : decrease by 20 when in small screen
    height: "140px",
    width: "140px",
    position: "relative",
    left: "50%",
    transform: "translateX(-50%)",
    [theme.breakpoints.down("xs")]: {
      height: "120px",
      width: "120px",
    },
  },
  netApy: {},
  netApyDescription: {
    display: "flex",
    flexDirection: "column",
    position: "absolute",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    margin: "0.1875rem",
    // backgroundColor: "#070A0E",
    backgroundColor: theme.palette.background.default,
    borderRadius: "50%",
    alignItems: "center",
    justifyContent: "center",
    padding: "0.25rem",

    "&:hover": {
      // backgroundColor: "rgba(20, 30, 39, 0.98)",
      backgroundColor: Color.rgb(theme.palette.background.default)
        .lighten(0.2)
        .hex(),
    },
  },
  netApyDescription_Active: {
    backgroundColor: "rgba(20, 30, 39, 0.98)",
  },
  label: {
    textAlign: "center",
    color: "#657786",

    // fontFamily: "Haas Grot Text R",
    fontStyle: "medium",
    fontWeight: 600,
    fontSize: "12px",
    // color: "#AAB8C1",
  },
  label_active: {
    color: "white",
  },
  headline: {
    fontWeight: 300,
    color: "#ffffff",
    marginTop: "0.7rem",
  },
  headline_inactive: {
    fontSize: "1.6rem",
  },
  headline_active: {
    color: "#F9FAFB",
    fontSize: "12px",
    textAlign: "center",
  },
}));

export const NetApy: React.FC<IProps> = (props) => {
  const classes = useStyles();
  const { netApy, totalYearlyChangeUsd } = props;

  // TODO : Add 'click detect' to keep same behavior on mobile
  const hoverTargetRef = useRef<HTMLDivElement>(null);
  const isHovering = useHover(hoverTargetRef);

  const isEarning = netApy > 0;
  const apyOrApr = isEarning ? "APY" : "APR";
  const apyDirectionMessage = `Net ${apyOrApr}`;
  const earnedOrPaid = isEarning ? "earned interest" : "paid interest";

  const netApyStringified = (+netApy.toFixed(2)).toLocaleString();
  const totalYearlyChangeUsdStringified = (+totalYearlyChangeUsd.toFixed(
    2
  )).toLocaleString();

  const labelText = isHovering
    ? `${totalYearlyChangeUsdStringified} $`
    : apyDirectionMessage;
  const headlineText = isHovering
    ? // ? "Interest earned and paid, plus TOKEN"
      `Anticipated yearly ${earnedOrPaid}`
    : `${netApyStringified}%`;

  return (
    <div className={classes.netApyWrapper} ref={hoverTargetRef}>
      <div className={classes.netApy}>
        <svg viewBox="0 0 140 140" width="100%">
          <path
            d="M 70 70 L  70 0 A 70 70 0 0 1 70 0 Z"
            stroke="transparent"
            fill="#9669ED"
          />
          <path
            d="M 70 70 L  70 0 A 70 70 0 1 1 69.99956017702848 1.381744718642608e-9 Z"
            stroke="transparent"
            fill={AFFIRMATIVE_GREEN_COLOR}
          />
        </svg>
      </div>
      <div
        className={clsx([
          classes.netApyDescription,
          isHovering ? classes.netApyDescription_Active : false,
        ])}
      >
        <label
          className={clsx([
            classes.label,
            isHovering ? classes.label_active : false,
          ])}
        >
          {labelText}
        </label>
        <div
          className={clsx([
            classes.headline,
            isHovering ? classes.headline_active : classes.headline,
          ])}
        >
          {headlineText}
        </div>
      </div>
    </div>
  );
};
