import { BigNumber } from "@ethersproject/bignumber";
import { action, computed, makeObservable, observable } from "mobx";
import {
  ITokenData,
  ITokenUnderlyingPrice,
  IUnderlyingMetadata,
  TAssetInMarketParameters,
} from "../../services/lendingNetworksService/ILendingNetworksService";
import {
  TCTokenBalances,
  TCTokenMetadata,
  TCTokenUnderlyingPrice,
} from "../../services/contractsIntegration/CompoundLensService/ICompoundLensService";
import { ethers } from "ethers";
import { DAILY_BLOCK_PER_CHAIN } from "../../constants/supportedTokensPerChain";
import { CryptoWalletConnectionStore } from "../CryptoWalletConnectionStore";

export class SingleCTokenStore {
  // CToken meta
  @observable cTokenAddress: string;
  @observable cTokenName: string;
  @observable cTokenSymbol: string;
  @observable cTokenDecimals: BigNumber;
  @observable exchangeRateCurrent: BigNumber;
  @observable supplyRatePerBlock: BigNumber;
  @observable borrowRatePerBlock: BigNumber;
  @observable private totalBorrows: BigNumber;
  @observable private totalReserves: BigNumber;
  @observable private totalSupply: BigNumber;
  @observable private totalCash: BigNumber;
  @observable private reserveFactorMantissa: BigNumber;
  @observable private collateralFactorMantissa: BigNumber;
  @observable isListed: boolean;

  @observable underlyingAssetAddress: string;
  @observable underlyingDecimals: number;
  @observable underlyingName: string;
  @observable underlyingSymbol: string;
  // @observable underlyingPrice: BigNumber;

  // CToken balances
  @observable private balanceOf: BigNumber; // CToken balance for the user
  @observable private borrowBalanceCurrent: BigNumber; // Underlying borrowed by the user
  @observable private balanceOfUnderlying: BigNumber; // Balance of underlying for the user in the cToken ( owed to the user, derived from the user's wallet  cToken balance ).
  @observable private tokenBalance: BigNumber; // Underlying balance for the user in their wallet
  @observable private tokenAllowance: BigNumber; // Allowance in underlying with cToken as spender

  // CToken underlying price
  @observable underlyingPrice: BigNumber;

  // Account specific data
  @observable isCollaterable: boolean;

  @computed get cTokenMeta(): TCTokenMetadata {
    const cTokenMeta: TCTokenMetadata = {
      cToken: this.cTokenAddress,
      cTokenDecimals: this.cTokenDecimals,
      collateralFactorMantissa: this.collateralFactorMantissa,
      exchangeRateCurrent: this.exchangeRateCurrent,
      isListed: this.isListed,
      reserveFactorMantissa: this.reserveFactorMantissa,
      totalBorrows: this.totalBorrows,
      totalReserves: this.totalReserves,
      totalSupply: this.totalSupply,
      underlyingAssetAddress: this.underlyingAssetAddress,
      underlyingDecimals: BigNumber.from(this.underlyingDecimals),
      borrowRatePerBlock: this.borrowRatePerBlock,
      supplyRatePerBlock: this.supplyRatePerBlock,
      totalCash: this.totalCash,
    };

    return cTokenMeta;
  }

  constructor(
    private cryptoWalletConnectionStore: CryptoWalletConnectionStore,
    assetInMarketParams: TAssetInMarketParameters
  ) {
    makeObservable(this);
    // this.cTokenAddress = tokenData.cToken;
    // this.cTokenName = tokenData.cTokenName;
    // this.cTokenSymbol = tokenData.cTokenSymbol;
    // this.cTokenDecimals = BigNumber.from(tokenData.cTokenDecimals);
    // this.totalCash = BigNumber.from(tokenData.totalCash);
    // this.supplyRatePerBlock = BigNumber.from(tokenData.supplyRatePerBlock);
    // this.borrowRatePerBlock = BigNumber.from(tokenData.borrowRatePerBlock);
    // this.underlyingAssetAddress = assetInMarketParams.address;
    // this.underlyingDecimals = assetInMarketParams.underlyingDecimals;
    // this.underlyingName = underlyingMetadata.underlyingName;
    // this.underlyingSymbol = underlyingMetadata.underlyingSymbol;
    // this.underlyingPrice = BigNumber.from(0); // TODO : Figure where to get this from ?

    this.cTokenAddress = "";
    this.cTokenName = "";
    this.cTokenSymbol = "";
    this.cTokenDecimals = BigNumber.from(0);
    this.exchangeRateCurrent = BigNumber.from(0);
    this.supplyRatePerBlock = BigNumber.from(0);
    this.borrowRatePerBlock = BigNumber.from(0);
    this.totalBorrows = BigNumber.from(0);
    this.totalReserves = BigNumber.from(0);
    this.totalSupply = BigNumber.from(0);
    this.totalCash = BigNumber.from(0);
    this.reserveFactorMantissa = BigNumber.from(0);
    this.collateralFactorMantissa = BigNumber.from(0);
    this.isListed = false;

    this.underlyingAssetAddress = assetInMarketParams.address;
    this.underlyingDecimals = assetInMarketParams.decimals;
    this.underlyingName = assetInMarketParams.name;
    this.underlyingSymbol = assetInMarketParams.symbol;

    // CToken balances
    this.balanceOf = BigNumber.from(0);
    this.borrowBalanceCurrent = BigNumber.from(0);
    this.balanceOfUnderlying = BigNumber.from(0);
    this.tokenBalance = BigNumber.from(0);
    this.tokenAllowance = BigNumber.from(0);

    // CToken underlying price
    this.underlyingPrice = BigNumber.from(0);

    // Account specific data
    this.isCollaterable = false;
  }

  // **** State refresh from outside ****
  public updateWithFreshState(
    cTokenMeta: TCTokenMetadata,
    cTokenBalances: TCTokenBalances,
    cTokenUnderlyingPrice: TCTokenUnderlyingPrice
  ) {
    this.batchUpdateFromCTokenMeta(
      cTokenMeta,
      cTokenBalances,
      cTokenUnderlyingPrice
    );
  }

  public updateIsInCollaterable(isCollaterable: boolean) {
    this.setIsCollaterable(isCollaterable);
  }

  /**
   * Calculates how much collateral is being generated from this market
   */
  @computed public get collateralPotentialInUSD(): number {
    if (this.isCollaterable && this.accountSupplyInUnits > 0) {
      console.log(`${this.underlyingSymbol} is collaterable`);

      const fullDepositedValueUsd =
        this.accountSupplyInUnits * this.underlyingUsdPrice;
      const collateralPotentialUsd =
        fullDepositedValueUsd * this.collateralFactorInUnits;

      return collateralPotentialUsd;
    } else {
      return 0;
    }
  }

  /***
   * Indicates how many underlying units the CToken currently holds
   */
  @computed
  public get cTokenCashInUnits(): number {
    return parseFloat(
      ethers.utils.formatUnits(this.totalCash, this.underlyingDecimals)
    );
  }

  /***
   * Indicates how many USD worth of underlying units the CToken currently holds
   */
  @computed
  public get cTokenCashInUSD(): number {
    return this.cTokenCashInUnits * this.underlyingUsdPrice;
  }

  @computed
  public get accountUnderlyingBalanceInUnits(): number {
    return parseFloat(
      ethers.utils.formatUnits(this.tokenBalance, this.underlyingDecimals)
    );
  }

  @computed
  public get totalSupplyInUnits(): number {
    return parseFloat(
      ethers.utils.formatUnits(this.totalSupply, this.underlyingDecimals)
    );
  }

  @computed
  public get accountSupplyInUnits(): number {
    return parseFloat(
      ethers.utils.formatUnits(
        this.balanceOfUnderlying,
        this.underlyingDecimals
      )
    );
  }

  @computed
  public get accountSupplyInOTokensRaw(): BigNumber {
    return this.balanceOf;
  }

  @computed
  public get accountSupplyInOTokensUnits(): number {
    return parseFloat(
      ethers.utils.formatUnits(
        this.accountSupplyInOTokensRaw,
        this.cTokenDecimals
      )
    );
  }

  @computed
  public get totalBorrowInUnits(): number {
    return parseFloat(
      ethers.utils.formatUnits(this.totalBorrows, this.underlyingDecimals)
    );
  }

  @computed
  public get accountBorrowInUnits(): number {
    return parseFloat(
      ethers.utils.formatUnits(
        this.borrowBalanceCurrent,
        this.underlyingDecimals
      )
    );
  }

  @computed
  public get underlyingUsdPrice(): number {
    // DEV_NOTE : This calculation is based on the 'Lens' which returns the price is `36 - asset.decimals'
    return parseFloat(
      ethers.utils.formatUnits(
        this.underlyingPrice,
        36 - this.underlyingDecimals
      )
    );
  }

  @computed
  public get totalSupplyBalanceUsd(): number {
    return this.totalSupplyInUnits * this.underlyingUsdPrice;
  }

  @computed
  public get accountSupplyBalanceUsd(): number {
    return this.accountSupplyInUnits * this.underlyingUsdPrice;
  }

  @computed
  public get totalBorrowBalanceUsd(): number {
    return this.totalBorrowInUnits * this.underlyingUsdPrice;
  }

  @computed
  public get accountBorrowBalanceUsd(): number {
    return this.accountBorrowInUnits * this.underlyingUsdPrice;
  }

  /**
   * We assume that if no cToken address has been set, it means that the 'batch update' has not finished.
   */
  @computed
  public get hasBeenInitialized(): boolean {
    return this.cTokenAddress !== "";
  }

  @computed
  public get collateralFactorInUnits(): number {
    const mantissaDecimals = 18;
    return parseFloat(
      ethers.utils.formatUnits(this.collateralFactorMantissa, mantissaDecimals)
    );
  }

  @computed
  public get supplyRatePerBlockInUnits(): number {
    const ratePerBlockDecimals = 18;
    return parseFloat(
      ethers.utils.formatUnits(this.supplyRatePerBlock, ratePerBlockDecimals)
    );
  }

  @computed
  public get borrowRatePerBlockInUnits(): number {
    const ratePerBlockDecimals = 18;
    return parseFloat(
      ethers.utils.formatUnits(this.borrowRatePerBlock, ratePerBlockDecimals)
    );
  }

  /***
   * Supply APY in units (e.g : 0.57 (%57))
   */
  @computed
  public get supplyApyInUnits(): number {
    const blockPerYear = this.cryptoWalletConnectionStore.blocksPerYear;

    return this.supplyRatePerBlockInUnits * blockPerYear;
  }

  /***
   * Borrow APY in units (e.g : 0.57 (%57))
   */
  @computed
  public get borrowApyInUnits(): number {
    const blockPerYear = this.cryptoWalletConnectionStore.blocksPerYear;

    return this.borrowRatePerBlockInUnits * blockPerYear;
  }

  /***
   * Supply APY in units (e.g : 0.57 (%57))
   */
  @computed
  public get supplyApyInPercentages(): number {
    return this.supplyApyInUnits * 100;
  }

  /***
   * Borrow APY in units (e.g : 0.57 (%57))
   */
  @computed
  public get borrowApyInPercentages(): number {
    return this.borrowApyInUnits * 100;
  }

  /**
   * Expected yearly revenue for supply interest.
   */
  @computed
  public get totalYearlyGainsUsd(): number {
    return this.accountSupplyInUnits * this.supplyApyInUnits;
  }

  /**
   * Expected yearly payment for borrow interest.
   */
  @computed
  public get totalYearlyLossesUsd(): number {
    return this.accountBorrowBalanceUsd * this.borrowApyInUnits;
  }

  /**
   * The expected change in value in a year.
   */
  @computed
  public get totalYearlyChangeUsd(): number {
    return this.totalYearlyGainsUsd - this.totalYearlyLossesUsd;
  }

  @computed public get hasAnyAllowance(): boolean {
    return this.tokenAllowance.gt(0);
  }

  public getUnitsForUsdValue(usdValue: number): number {
    return usdValue / this.underlyingUsdPrice;
  }

  // **** Complex setters ****

  @action("batchUpdateFromCTokenMeta")
  private batchUpdateFromCTokenMeta(
    cTokenMeta: TCTokenMetadata,
    cTokenBalances: TCTokenBalances,
    cTokenUnderlyingPrice: TCTokenUnderlyingPrice
  ) {
    this.cTokenAddress = cTokenMeta.cToken;
    this.cTokenDecimals = cTokenMeta.cTokenDecimals;
    // this.cTokenName = cTokenMeta.;
    // this.cTokenSymbol = "";

    this.exchangeRateCurrent = cTokenMeta.exchangeRateCurrent;
    this.supplyRatePerBlock = cTokenMeta.supplyRatePerBlock;
    this.borrowRatePerBlock = cTokenMeta.borrowRatePerBlock;
    this.totalBorrows = cTokenMeta.totalBorrows;
    this.totalReserves = cTokenMeta.totalReserves;
    this.totalSupply = cTokenMeta.totalSupply;
    this.totalCash = cTokenMeta.totalCash;
    this.collateralFactorMantissa = cTokenMeta.collateralFactorMantissa;
    this.isListed = cTokenMeta.isListed;

    // CToken Balances
    this.balanceOf = cTokenBalances.balanceOf;
    this.borrowBalanceCurrent = cTokenBalances.borrowBalanceCurrent;
    this.balanceOfUnderlying = cTokenBalances.balanceOfUnderlying;
    this.tokenBalance = cTokenBalances.tokenBalance;
    this.tokenAllowance = cTokenBalances.tokenAllowance;

    // CToken underlying price
    this.underlyingPrice = cTokenUnderlyingPrice.underlyingPrice;
  }

  // **** Action setters ****
  @action("setIsCollaterable")
  private setIsCollaterable(isCollaterable: boolean) {
    this.isCollaterable = isCollaterable;

    if (this.isCollaterable)
      console.log(
        `${this.underlyingSymbol} is collaterable with balance of ${this.totalSupplyBalanceUsd}`
      );
  }
}
