import { BigNumber, Contract, ContractTransaction } from "ethers";
import { Signer } from "@ethersproject/abstract-signer";
import { Provider } from "@ethersproject/abstract-provider";
import CErc20Json from "../../../solidityInterfaces/CErc20.json";
import { ICErc20Service } from "./ICErc20Service";
import { CErc20 } from "../../../solidityInterfaces/CErc20";

export class CErc20Service implements ICErc20Service {
  private cErc20Contract: CErc20;

  constructor(erc20Address: string, signerOrProvider: Signer | Provider) {
    this.cErc20Contract = new Contract(
      erc20Address,
      CErc20Json.abi,
      signerOrProvider
    ) as CErc20;
  }

  public async mint(amount: BigNumber): Promise<ContractTransaction> {
    return this.cErc20Contract.mint(amount);
  }

  public async withdraw(amount: BigNumber): Promise<ContractTransaction> {
    return this.cErc20Contract.redeemUnderlying(amount);
  }

  public async withdrawByOTokens(
    oTokensAmount: BigNumber
  ): Promise<ContractTransaction> {
    return this.cErc20Contract.redeem(oTokensAmount);
  }

  public async borrow(amount: BigNumber): Promise<ContractTransaction> {
    return this.cErc20Contract.borrow(amount);
  }

  public async repayBorrow(amount: BigNumber): Promise<ContractTransaction> {
    return this.cErc20Contract.repayBorrow(amount);
  }
}
