import { makeStyles, Typography } from "@material-ui/core";
import clsx from "clsx";
import React from "react";
import { AFFIRMATIVE_GREEN_COLOR } from "../../../../theme/themeConstants";
import { AnimatedNumberText } from "../../../../components/AnimatedNumberText/AnimatedNumberText";

interface IProps {
  titleText: string;
  sum: number;
  isSupply: boolean;
  alignLeft: boolean;
}

const useStyles = makeStyles((theme) => ({
  headline: {
    fontSize: "1.6rem",
    fontWeight: 300,
    color: "#ffffff",
    marginTop: "0.7rem",
  },
  textCenter: {
    textAlign: "center",
  },
  textLeft: {
    textAlign: "left",
  },
  label: {
    // fontFamily: "Haas Grot Text R",
    fontStyle: "medium",
    fontWeight: 600,
    fontSize: "12px",
    color: "#AAB8C1",
  },
  supply: {
    color: AFFIRMATIVE_GREEN_COLOR,
  },
  borrow: {
    // color: "#9669ED",
    // color: "#ff8906",
    color: "#f25f4c",
  },
}));

export const BalanceHeadline = React.memo<IProps>((props) => {
  const classes = useStyles();
  const { titleText, sum, isSupply, alignLeft } = props;

  return (
    <div className={alignLeft ? classes.textLeft : classes.textCenter}>
      <label
        className={clsx([
          classes.label,
          isSupply ? classes.supply : classes.borrow,
        ])}
      >
        {titleText}
      </label>
      {/* TODO : Add the digits CSS */}
      <div className={classes.headline}>
        $
        <AnimatedNumberText value={sum} />
        {/*{sum.toLocaleString()}*/}
      </div>
    </div>
  );
});
