import { makeStyles } from "@material-ui/core";
import React from "react";
import { BalancesAndApy } from "./BalancesAndApy";
import { BorrowLimitBar } from "./BorrowLimitBar";

interface IProps {
  supplyBalanceUsd: number;
  borrowBalanceUsd: number;
  netApy: number;
  totalYearlyChangeUsd: number;

  // Borrow limit
  usedBorrowPercentage: number;
  collateralPotentialInUSD: number;
}

const useStyles = makeStyles((theme) => ({
  balancesTotal: {
    position: "relative",
    maxWidth: "60em",
    margin: "0 auto",
    [theme.breakpoints.down("xs")]: {
      margin: "0 1.33rem",
    },
  },
}));

export const BalancesTotal = React.memo<IProps>((props) => {
  const classes = useStyles();

  const {
    supplyBalanceUsd,
    netApy,
    totalYearlyChangeUsd,
    borrowBalanceUsd,
    usedBorrowPercentage,
    collateralPotentialInUSD,
  } = props;

  return (
    <div className={classes.balancesTotal}>
      <BalancesAndApy
        netApy={netApy}
        totalYearlyChangeUsd={totalYearlyChangeUsd}
        supplyBalanceUsd={supplyBalanceUsd}
        borrowBalanceUsd={borrowBalanceUsd}
      />
      <BorrowLimitBar
        usedBorrowPercentage={usedBorrowPercentage}
        collateralPotentialInUSD={collateralPotentialInUSD}
      />
    </div>
  );
});
