import React, { ReactNode, useMemo } from "react";
import {
  Button,
  Fade,
  IconButton,
  makeStyles,
  Modal,
  Typography,
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";
import { TokenIcon } from "../../../icons/TokenIcon";
import { TModalFooterMessagePayload } from "./ModalFooter";
import { ModalActionButton } from "./ModalActionButton";
import { ActionRates } from "./rates/ActionRates";
import {
  BACKGROUND_LIGHT_COLOR,
  BRAND_MAIN_COLOR,
  TEXT_ON_LIGHT,
} from "../../../../theme/themeConstants";
import { BorrowLimitRates } from "./rates/BorrowLimitRates";
import {
  TTxWaitingStage,
  TxWaitingModal,
} from "../../txWaitingModal/TxWaitingModal";
import Color from "color";

export type TZeroOrOne = 0 | 1;

export interface IMarketModalCommonStatePayload {
  // Tabs
  currentTabIndex: TZeroOrOne;
  onTabIndexChange: (newIndex: TZeroOrOne) => void;
  tab0Title: string;
  tab1Title: string;

  // Assets
  actionAssetSymbol: string;
  actionAssetName: string;
  distributionAssetSymbol: string;
  assetExplorerLink: string;

  // Borrow Limit (optional)
  currentBorrowLimit?: number;
  usedBorrowLimitPercentage?: number;

  // TX waiting
  explorerName: string;
  explorerUrlBaseForTx: string;
  activeTxHash?: string;
  waitingForUserApproval?: boolean;
  waitingForTxConfirmation?: boolean;
}

export interface IMarketModalPerTabStatePayload {
  // Message or input component
  messageOrInput: ReactNode;
  showInputActionButton: boolean;
  inputActionButtonActive: boolean;
  inputActionButtonText: string;
  onInputActionButtonPressed?: () => void;

  // Action Rates
  actionName: "Borrow" | "Supply";
  actionApy: number;
  distributionApy: number;

  // Borrow Limit (optional)
  showBorrowLimit?: boolean;
  showPredictedBorrowLimits?: boolean;
  borrowLimitAfterChange?: number;
  usedBorrowLimitPercentageAfterChange?: number;
  borrowLimitNote?: string;

  // Action button
  actionButtonActive: boolean;
  actionButtonText: string;
  actionButtonDisabledText?: string;
  actionButtonOnClick: () => void;

  // Footer
  footerMessagesPayloads: TModalFooterMessagePayload[];
  // footerTitle: string | string[];
  // footerTokenAmount: number;
}

// DEV_NOTE : To make it easier to build in parent components
export interface IMarketModalStatePayload
  extends IMarketModalCommonStatePayload,
    IMarketModalPerTabStatePayload {}

interface IProps extends IMarketModalStatePayload {
  open: boolean;
  closeModal: () => void;
}

const TOP_DISTANCE_REM = 0.5;

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    color: TEXT_ON_LIGHT,
  },
  // paper: {
  //   backgroundColor: theme.palette.background.paper,
  //   // border: "2px solid #000",
  //   boxShadow: theme.shadows[5],
  //   padding: theme.spacing(20, 4, 3),
  // },
  marketInteractionPanel: {
    position: "relative",
    width: "25.5rem",
    maxWidth: "100%",
    backgroundColor: BACKGROUND_LIGHT_COLOR,
  },
  panelHeader: {
    width: "100%",
    // color: "#141E27",
    display: "flex",
    flexDirection: "row",
    justifyContent: "center",
    alignItems: "center",
    borderBottom: "1px solid rgba(0, 0, 0, 0.05)",

    padding: `${TOP_DISTANCE_REM * 2}rem`,

    // Mobile
    [theme.breakpoints.down("sm")]: {
      // padding: "1.0rem",
      // display: "flex",
      // flexDirection: "row",
      // alignItems: "center",
    },
  },
  title: {},
  tokenIcon: {
    marginRight: "0.75rem",
    width: "2.5rem",
    height: "2.5rem",
    position: "fixed",
    left: 0,
    transition: "200ms",
    cursor: "pointer",
    "&:hover": {
      transform: "scale(1.1)",
    },
  },
  upperRightCorner: {
    position: "absolute",
    right: `${TOP_DISTANCE_REM / 2}rem`,
    top: `${TOP_DISTANCE_REM / 2}rem`,
    // Mobile
    [theme.breakpoints.down("sm")]: {
      right: `${TOP_DISTANCE_REM / 8}rem`,
      top: `${TOP_DISTANCE_REM / 8}rem`,
    },
  },
  upperLeftCorner: {
    position: "absolute",
    left: `${TOP_DISTANCE_REM}rem`,
    top: `${TOP_DISTANCE_REM}rem`,
  },

  tabBody: {
    paddingTop: "2rem",
    paddingBottom: "1rem",
    paddingLeft: "1.5rem",
    paddingRight: "1.5rem",
    // backgroundColor: "#ffffff",
    backgroundColor: new Color(BACKGROUND_LIGHT_COLOR)
      .mix(Color.rgb([0, 0, 0]), 0.02)
      .hex(),
  },
}));

export const MarketInteractionModal = React.memo<IProps>((props) => {
  const {
    open,
    closeModal,

    // Input/message
    messageOrInput,
    inputActionButtonText,
    onInputActionButtonPressed,
    showInputActionButton,
    inputActionButtonActive,

    // Assets
    actionAssetSymbol,
    actionAssetName,
    distributionAssetSymbol,

    // Tabs
    currentTabIndex,
    onTabIndexChange,
    tab0Title,
    tab1Title,

    // Action Rates
    actionName,
    actionApy,
    distributionApy,

    // Borrow Limits (Optional)
    showBorrowLimit,
    showPredictedBorrowLimits,
    currentBorrowLimit,
    borrowLimitAfterChange,
    usedBorrowLimitPercentage,
    usedBorrowLimitPercentageAfterChange,
    borrowLimitNote,

    // Action button
    actionButtonActive,
    actionButtonText,
    actionButtonDisabledText,
    actionButtonOnClick,

    // Footer
    footerMessagesPayloads,

    // TX Waiting
    assetExplorerLink,
    explorerName,
    explorerUrlBaseForTx,
    activeTxHash,
    waitingForUserApproval,
    waitingForTxConfirmation,
  } = props;

  const classes = useStyles();

  const isSupply = actionName === "Supply";
  const isBorrow = actionName === "Borrow";

  const formattedMessages = useMemo(() => {}, []);

  if (waitingForTxConfirmation || waitingForUserApproval) {
    const txWaitingStage: TTxWaitingStage = waitingForTxConfirmation
      ? "CONFIRMATION"
      : "USER_APPROVAL";

    return (
      <TxWaitingModal
        open={open}
        closeModal={closeModal}
        txWaitingStage={txWaitingStage}
        txHash={activeTxHash}
        explorerUrlBaseForTx={explorerUrlBaseForTx}
        explorerName={explorerName}
      />
    );
  }

  return (
    <Modal open={open} className={classes.modal} onBackdropClick={closeModal}>
      <Fade in={open}>
        <div className={classes.marketInteractionPanel}>
          {/* Quick-n-Dirty x button */}
          <div className={classes.upperRightCorner}>
            <IconButton onClick={closeModal}>
              <CloseIcon />
            </IconButton>
          </div>

          {/* Quick-n-Dirty Market icon */}
          <a
            target={"_blank"}
            rel={"noreferrer noopener"}
            href={assetExplorerLink}
          >
            <TokenIcon
              symbol={actionAssetSymbol}
              extraClasses={[classes.tokenIcon, classes.upperLeftCorner]}
            />
          </a>

          {/* Header */}
          <div className={classes.panelHeader}>
            <Typography variant={"h5"} className={classes.title}>
              {actionAssetName}
            </Typography>
          </div>

          {/* Input or message */}
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            {messageOrInput}

            {showInputActionButton && (
              <Button
                color={"primary"}
                style={{ marginRight: "1.5rem", maxHeight: "3rem" }}
                variant={"outlined"}
                onClick={onInputActionButtonPressed}
                disabled={!inputActionButtonActive}
              >
                <Typography variant={"body2"}>
                  {inputActionButtonText}
                </Typography>
              </Button>
            )}
          </div>

          {/* Tabs */}
          <div
            style={{
              display: "flex",
              flexDirection: "row",
            }}
          >
            <div
              style={{
                flex: 1,
                textAlign: "center",
                borderBottom:
                  currentTabIndex === 0
                    ? `2px solid ${BRAND_MAIN_COLOR}`
                    : "none",
              }}
            >
              <Button fullWidth onClick={() => onTabIndexChange(0)}>
                <Typography>{tab0Title}</Typography>
              </Button>
            </div>
            <div
              style={{
                flex: 1,
                textAlign: "center",
                borderBottom:
                  currentTabIndex === 1
                    ? `2px solid ${BRAND_MAIN_COLOR}`
                    : "none",
              }}
            >
              <Button fullWidth onClick={() => onTabIndexChange(1)}>
                <Typography>{tab1Title}</Typography>
              </Button>
            </div>
          </div>

          {/* Tab Body */}
          <div className={classes.tabBody}>
            {/* Modal action rates */}
            <ActionRates
              actionName={actionName}
              assetSupplyApy={actionApy}
              assetSymbol={actionAssetSymbol}
              distributionAssetApy={distributionApy}
              distributionAssetSymbol={distributionAssetSymbol}
            />
            <br />

            {/* Borrow limit rates */}
            {showBorrowLimit && (
              <>
                <br />
                <BorrowLimitRates
                  limitOrBalance={isBorrow ? "Balance" : "Limit"}
                  borrowLimitUsd={currentBorrowLimit || 0}
                  borrowLimitUsedPercentage={usedBorrowLimitPercentage || 0}
                  predictedBorrowLimitPercentage={
                    usedBorrowLimitPercentageAfterChange || 0
                  }
                  predictedBorrowLimitUsd={borrowLimitAfterChange || 0}
                  displayPredictedValues={showPredictedBorrowLimits}
                  note={borrowLimitNote}
                />
                <br />
              </>
            )}

            <br />
            {/* Action button */}
            <ModalActionButton
              onClick={actionButtonOnClick}
              text={
                actionButtonActive || !actionButtonDisabledText
                  ? actionButtonText
                  : actionButtonDisabledText
              }
              disabled={!actionButtonActive}
            />

            <br />
            <br />

            {/* DEV_NOTE : Footer inside of body to keep same padding easily */}
            {/*{footerMessagesPayloads.map((footerMessagesPayload) => (*/}
            {/*  <>*/}
            {/*    <ModalFooter*/}
            {/*      key={footerMessagesPayload.title}*/}
            {/*      title={footerMessagesPayload.title}*/}
            {/*      tokenAmount={footerMessagesPayload.tokenAmount}*/}
            {/*      tokenSymbol={footerMessagesPayload.tokenSymbol}*/}
            {/*    />*/}
            {/*    <br key={`br_${footerMessagesPayload.title}`} />*/}
            {/*  </>*/}
            {/*))}*/}
          </div>
        </div>
      </Fade>
    </Modal>
  );
});
