import React, { useMemo } from "react";
import { makeStyles } from "@material-ui/core";
import { BaseRates, TRatesRow } from "./BaseRates";

interface IProps {
  limitOrBalance: "Limit" | "Balance";
  borrowLimitUsd: number;
  borrowLimitUsedPercentage: number;
  predictedBorrowLimitUsd: number;
  predictedBorrowLimitPercentage: number;
  displayPredictedValues?: boolean;
  note?: string;
}

export const BorrowLimitRates = React.memo<IProps>((props) => {
  const {
    limitOrBalance,
    borrowLimitUsd,
    borrowLimitUsedPercentage,
    predictedBorrowLimitUsd,
    predictedBorrowLimitPercentage,
    displayPredictedValues,
    note,
  } = props;

  const rateRows = useMemo<TRatesRow[]>(() => {
    const firstRow: TRatesRow = {
      label: `Borrow ${limitOrBalance}`,
      value: borrowLimitUsd,
      valueSuffix: "$",
      displayPredictedValues: displayPredictedValues,
      predictedValue: predictedBorrowLimitUsd,
    };
    const secondRow: TRatesRow = {
      label: "Borrow Limit Used",
      value: borrowLimitUsedPercentage,
      valueSuffix: "%",
      displayPredictedValues: displayPredictedValues,
      predictedValue: predictedBorrowLimitPercentage,
    };

    return [firstRow, secondRow];
  }, [
    borrowLimitUsd,
    borrowLimitUsedPercentage,
    displayPredictedValues,
    limitOrBalance,
    predictedBorrowLimitPercentage,
    predictedBorrowLimitUsd,
  ]);

  const title = "Borrow Limit";
  const subTitle = note;

  return (
    <BaseRates
      title={title}
      subTitle={subTitle}
      rateRows={rateRows}
      bottomBorder
    />
  );
});
