import { StylesProvider, ThemeProvider } from "@material-ui/core";
import React from "react";
import { App } from "./App";
import { baseTheme } from "./theme/Theme";
import { buildLNServices, buildServices } from "./services/Services";
import { configureMobx, getStores } from "./store/storesInitialization";
import { Provider } from "mobx-react";
import { ZERO_ADDRESS } from "./constants/constantAddresses";

interface IProps {}

configureMobx();

// TODO : Move this to a better place
const ethereumProvider = (window as any).ethereum;
const services = buildServices(ethereumProvider);
const lnServices = buildLNServices(ethereumProvider, {
  unitrollerAddress: ZERO_ADDRESS,
});
// process.env.NODE_ENV === "production"
//   ? buildServices(ethereumProvider)
//   : buildMockServices(ethereumProvider);
const stores = getStores(
  services.cryptoWalletIntegrationService,
  services.lendingNetworksService,
  services.erc20ServiceBuilder,
  services.cTokenServiceBuilder
);

export const AppWrapper = React.memo<IProps>((props) => {
  return (
    <Provider {...stores} {...services} {...lnServices}>
      <StylesProvider injectFirst>
        <ThemeProvider theme={baseTheme}>
          <App />
        </ThemeProvider>
      </StylesProvider>
    </Provider>
  );
});
