import { Contract } from "ethers";
import { Signer } from "@ethersproject/abstract-signer";
import { Provider } from "@ethersproject/abstract-provider";
import UniswapAnchoredViewJson from "../../../solidityInterfaces/UniswapAnchoredView.json";
import {
  IUniswapAnchoredViewService,
  TTokenConfig,
} from "./IUniswapAnchoredViewService";
import { UniswapAnchoredView } from "../../../solidityInterfaces/UniswapAnchoredView";
import { etheresStructResponseToObject } from "../contractsIntegrationUtils";

export class UniswapAnchoredViewService implements IUniswapAnchoredViewService {
  private uniswapAnchoredViewContract: UniswapAnchoredView;

  constructor(
    uniswapAnchoredViewAddress: string,
    signerOrProvider?: Signer | Provider
  ) {
    this.uniswapAnchoredViewContract = new Contract(
      uniswapAnchoredViewAddress,
      UniswapAnchoredViewJson.abi,
      signerOrProvider
    ) as UniswapAnchoredView;
  }

  /**
   * @ DEV_NOTE : Basic example for contract reading function.
   */
  async getTokenConfigBySymbol(tokenSymbol: string): Promise<TTokenConfig> {
    return etheresStructResponseToObject(
      await this.uniswapAnchoredViewContract.getTokenConfigBySymbol(tokenSymbol)
    );
  }
}
