import { makeStyles } from "@material-ui/core";
import clsx from "clsx";
import React from "react";
import { BalancesTotal } from "./BalancesTotal";
import { useActiveLendingNetworkStore } from "../../../../store/storeHooks";
import { observer } from "mobx-react";

interface IProps {}

const useStyles = makeStyles((theme) => ({
  hero: {
    // background: "#070A0E",
    backgroundColor: theme.palette.background.default,
    marginBottom: "-3.75rem",
    padding: "1.2635rem 0 7.315rem",
  },
}));

export const BorrowOverview = observer<React.FunctionComponent<IProps>>(
  (props) => {
    const classes = useStyles();

    const lnStore = useActiveLendingNetworkStore();

    const supplyBalanceUsd = lnStore?.totalAccountSupplyBalanceUsd || 0;
    const borrowBalanceUsd = lnStore?.totalAccountBorrowBalanceUsd || 0;
    const netApy = lnStore?.netApy || 0;
    const totalYearlyChangeUsd = lnStore?.totalYearlyChangeUsd || 0;
    const collateralPotentialInUSD = lnStore?.collateralPotentialInUSD || 0;
    const usedBorrowPercentage = lnStore?.usedBorrowPercentage || 0;

    return (
      <section className={clsx(classes.hero)}>
        <BalancesTotal
          supplyBalanceUsd={supplyBalanceUsd}
          borrowBalanceUsd={borrowBalanceUsd}
          netApy={netApy}
          totalYearlyChangeUsd={totalYearlyChangeUsd}
          usedBorrowPercentage={usedBorrowPercentage}
          collateralPotentialInUSD={collateralPotentialInUSD}
        />
      </section>
    );
  }
);
