import React, { useMemo } from "react";
import clsx from "clsx";
import { useCommonClasses } from "../../theme/commonClasses";
import { makeStyles } from "@material-ui/core";

interface IProps {
  symbol: string;
  extraClasses?: string[];
}

const PUBLIC_URL = process.env.PUBLIC_URL;

const useStyles = makeStyles((theme) => ({
  token: {
    // "&:hover": {
    //   animation: "$turn 3000ms linear",
    // },
  },

  "@keyframes turn": {
    "0%": {
      transform: "rotateY(0deg)",
    },
    // "25%": {
    //   transform: "rotateY(-180deg)",
    // },
    "50%": {
      transform: "rotateY(-360deg)",
    },
    // "75%": {
    //   transform: "rotateY(180deg)",
    // },
    "100%": {
      transform: "rotateY(0deg)",
    },
  },
  "@keyframes return": {
    "100%": {
      transform: "rotateY(0deg)",
    },
  },
}));

export const TokenIcon = React.memo<IProps>((props) => {
  const { symbol, extraClasses } = props;
  const classes = useStyles();
  const commonClasses = useCommonClasses();

  const classesForSpan = useMemo(() => {
    const safeExtraClasses = extraClasses || [];
    return clsx([commonClasses.icon, classes.token, ...safeExtraClasses]);
  }, [classes.token, commonClasses.icon, extraClasses]);

  return (
    <span
      className={classesForSpan}
      style={{
        backgroundImage: `url(${PUBLIC_URL}/tokens/asset_${symbol}.svg)`,
        backgroundSize: "contain",
        backgroundRepeat: "no-repeat",
      }}
    />
  );
});
