export const Header = observer<React.FunctionComponent<IProps>>((props) => {
  const classes = useStyles();

  const cryptoWalletConnectionStore = useCryptoWalletIntegrationStore();
  const modalsStore = useModalsStore();
  const lendingNetworksStore = useLendingNetworksStore();
  const activeLn = lendingNetworksStore.activeLNStore;

  const walletText = cryptoWalletConnectionStore.isConnectedToWallet
    ? summarizeAddress(cryptoWalletConnectionStore.mainAddress)
    : "Connect";

  const hasActiveLn = !!activeLn;
  const activeLnName = activeLn?.name;
  const linkToLnPage = activeLn?.lnHomePage || "/";
  const onlyIncentiveTokenBalance = activeLn?.compBalanceUnits || 0;
  // DEV_NOTE : This is a quick-n-dirty patch to display both wFuse (incentive) and fuse together.
  const balanceToShow = useMemo(() => {
    if (activeLnName === LENDING_NETWORK_NAMES.fuseAlpha) {
      return (
        cryptoWalletConnectionStore.nativeCoinBalance +
        onlyIncentiveTokenBalance
      );
    } else {
      return onlyIncentiveTokenBalance;
    }
  }, [
    activeLnName,
    cryptoWalletConnectionStore.nativeCoinBalance,
    onlyIncentiveTokenBalance,
  ]);

  return (
    <header className={classes.header}>
      <ContentContainer>
        <Grid
          container
          direction="row"
          alignItems="center"
          style={
            {
              // alignItems: "center",
              // alignContent: "center",
            }
          }
        >
          {/* Logo */}
          <Grid item xs={6} sm={3} className={classes.alignedRow}>
            {/* TODO : Add Project logo here */}
            <Link className={classes.logoLink} to="/">
              <div className={classes.logoSpan} />
            </Link>
            {/*<Link className={classes.logoLinkText} to="/">*/}
            {/*  <span className={clsx([classes.letterO, classes.logoLetter])}>*/}
            {/*    O*/}
            {/*  </span>*/}
            {/*  <span className={clsx([classes.letterL, classes.logoLetter])}>*/}
            {/*    L*/}
            {/*  </span>*/}
            {/*  <span className={clsx([classes.letterA, classes.logoLetter])}>*/}
            {/*    A*/}
            {/*  </span>*/}
            {/*</Link>*/}

            {/* TODO : QUICK-HACK : ORGANIZE THIS*/}
            {activeLnName && (
              <a
                className={classes.lnLinkText}
                target="_blank"
                href={linkToLnPage}
                style={{
                  fontFamily: "Gotham A, Gotham B, sans-serif",
                }}
              >
                {activeLnName}
              </a>
            )}
            {/*{!activeLnName && (*/}
            {/*  <span className={classes.lnLinkText}>All Networks</span>*/}
            {/*)}*/}
          </Grid>
          {/* Empty spaces */}
          {/* DEV_NOTE : Place holder for the navigations */}
          <Hidden xsDown>
            <Grid item xs={6}></Grid>
          </Hidden>

          <Grid
            className={clsx(classes.alignedRow, classes.actions)}
            item
            xs={12}
            sm={3}
          >
            {/* Token balance */}
            {hasActiveLn && (
              <div
                className={clsx([
                  classes.headerButtonLike,
                  classes.tokenBalance,
                ])}
                onClick={() => modalsStore.showIncentiveModal()}
              >
                {balanceToShow.toFixed(4).toLocaleString()}
                <div
                  className={clsx(
                    classes.tokenIcon,
                    classes.iconComp,
                    classes.icon
                  )}
                />
              </div>
            )}

            {/* Address */}
            {/* DEV_NOTE : Hides on mobile to keep original UX*/}
            {/*<Hidden xsDown>*/}
            <a
              className={clsx(classes.headerButtonLike, classes.account)}
              onClick={() => cryptoWalletConnectionStore.askToConnect()}
            >
              <span />
              {walletText}
            </a>
            {/*</Hidden>*/}
          </Grid>
        </Grid>
      </ContentContainer>
    </header>
  );
});

/**
 * QuicknDirty
 */
function summarizeAddress(address: string): string {
  return `${address.substring(0, 3)}...${address.substring(
    address.length - 3,
    address.length
  )}`;
}
