  const getPayloadForMarketInteractionModal = useCallback(
    (baseAssetAddress: string) => {
      if (!lnStore) {
        return {
          cTokenMeta: undefined,
          assetParameters: undefined,
        };
      }
      const cTokenStore = lnStore.baseAssetsToCTokenStore.get(baseAssetAddress);
      const assetParameters = lnStore.assetsParams.find(
        (assetParam) => assetParam.address === baseAssetAddress
      );

      if (!assetParameters) {
        console.error(`No asset parameters found for ${baseAssetAddress}`);
      } else if (!cTokenStore) {
        console.error(`No cToken meta found for ${baseAssetAddress}`);
      }

      const payload: {
        assetParameters: TAssetInMarketParameters | undefined;
        cTokenMeta: TCTokenMetadata | undefined;
      } = {
        assetParameters,
        cTokenMeta: undefined,
      };

      return payload;
    },
    [lnStore]
  );

