import React, { useCallback, useMemo } from "react";
import { observer } from "mobx-react";
import { CollateralModal } from "./CollateralModal";
import { useCalculateChangeInBorrowLimits } from "../MarketInteractionModal/marketInteractionModalHooks";
import { useCollateralModalState } from "./collateralModalHooks";
import { useTxWaitingLogic } from "../txWaitingModal/txWaitingHooks";
import {
  TTxWaitingStage,
  TxWaitingModal,
} from "../txWaitingModal/TxWaitingModal";
import { useSystemStore } from "../../../store/storeHooks";

interface IProps {
  open: boolean;
  closeModal: () => void;
}

export const ConnectedCollateralModal = observer<
  React.FunctionComponent<IProps>
>((props) => {
  const { open, closeModal } = props;

  const systemStore = useSystemStore();

  const {
    activeLNStore,
    cToken,
    accountSupplyInUnits,
    underlyingName,
    underlyingSymbol,
    isCollaterable,

    priceCalculationDecimals,
    usedBorrowPercentage,
    totalAccountBorrowBalanceUsd,
    currentBorrowLimitUsd,
    collateralFactorInUnits,
    pricePerUnitUsd,
  } = useCollateralModalState();

  const newCollaterableValue = !isCollaterable;
  // Will the new state add or reduce potential collateral
  const signMultiplier = newCollaterableValue ? 1 : -1;
  const effectiveUnits = signMultiplier * accountSupplyInUnits;

  const actionButtonText = newCollaterableValue
    ? `Use ${underlyingSymbol} as collateral`
    : `Remove ${underlyingSymbol} as collateral`;

  const actionName = newCollaterableValue ? "Enable" : "Remove";

  // Calculate the changes to borrow limit (USD and % used)
  const {
    collateralValueOfActionEffect,
    borrowLimitAfterChange,
    usedBorrowLimitPercentageAfterChange,
  } = useCalculateChangeInBorrowLimits(
    currentBorrowLimitUsd,
    totalAccountBorrowBalanceUsd,
    effectiveUnits,
    pricePerUnitUsd,
    collateralFactorInUnits,
    priceCalculationDecimals
  );

  // We always want to emphasis the change (even if it is 0)
  const showPredictedBorrowLimits = true;

  const actionButtonActive = true;

  const txCreatingFunction = useMemo(() => {
    if (newCollaterableValue) {
      return () => activeLNStore?.enterMarketByCTokenAddress(cToken);
    } else {
      return () => activeLNStore?.exitMarketByCTokenAddress(cToken);
    }
  }, [activeLNStore, cToken, newCollaterableValue]);

  const {
    activeTxHash,
    wrappedTxBuildingFunction,
    waitingForUserApproval,
    waitingForTxConfirmation,
  } = useTxWaitingLogic(txCreatingFunction, closeModal, true);

  const onActionButtonClicked = useCallback(async () => {
    await wrappedTxBuildingFunction();
  }, [wrappedTxBuildingFunction]);

  if (waitingForTxConfirmation || waitingForUserApproval) {
    const txWaitingStage: TTxWaitingStage = waitingForTxConfirmation
      ? "CONFIRMATION"
      : "USER_APPROVAL";

    return (
      <TxWaitingModal
        open={open}
        closeModal={closeModal}
        txWaitingStage={txWaitingStage}
        txHash={activeTxHash}
        explorerUrlBaseForTx={systemStore.explorerBaseTxUrl}
        explorerName={systemStore.explorerName}
      />
    );
  }

  return (
    <CollateralModal
      open={open}
      closeModal={closeModal}
      actionName={actionName}
      actionButtonText={actionButtonText}
      actionAssetName={underlyingName}
      actionAssetSymbol={underlyingSymbol}
      actionButtonActive={actionButtonActive}
      actionButtonOnClick={onActionButtonClicked}
      showPredictedBorrowLimits={showPredictedBorrowLimits}
      borrowLimitAfterChange={borrowLimitAfterChange}
      currentBorrowLimit={currentBorrowLimitUsd}
      usedBorrowLimitPercentage={usedBorrowPercentage}
      usedBorrowLimitPercentageAfterChange={
        usedBorrowLimitPercentageAfterChange
      }
      waitingForUserApproval={waitingForUserApproval}
      waitingForTxConfirmation={waitingForTxConfirmation}
    />
  );
});
