import { ContractTransaction } from "ethers";
import { useCallback, useState } from "react";
import { JSON_RPC_ERROR_CODES } from "../../../constants/ethereumErrorCodes";

export type TTxCreatingFunction = () =>
  | Promise<ContractTransaction>
  | undefined;

export function useTxWaitingLogic(
  txCreatingFunction: TTxCreatingFunction,
  closeModal: () => void,
  closeModalOnSuccess: boolean
) {
  const {
    activeTxHash,
    setActiveTxHash,
    waitingForTxConfirmation,
    waitingForUserApproval,
    setWaitingForTxConfirmation,
    setWaitingForUserApproval,
  } = useTxWaitingStateExplicit();

  const wrappedTxBuildingFunction = useTxWaitingLogicExplicit(
    txCreatingFunction,
    closeModal,
    setActiveTxHash,
    setWaitingForUserApproval,
    setWaitingForTxConfirmation,
    closeModalOnSuccess
  );

  return {
    activeTxHash,
    waitingForUserApproval,
    waitingForTxConfirmation,
    wrappedTxBuildingFunction,
  };
}

export function useTxWaitingStateExplicit() {
  const [activeTxHash, setActiveTxHash] = useState("");
  const [waitingForUserApproval, setWaitingForUserApproval] = useState(false);
  const [waitingForTxConfirmation, setWaitingForTxConfirmation] = useState(
    false
  );

  return {
    activeTxHash,
    setActiveTxHash,
    waitingForUserApproval,
    waitingForTxConfirmation,
    setWaitingForUserApproval,
    setWaitingForTxConfirmation,
  };
}

/**
 * Wraps a call to a tx creating function with some UX logic of waiting for user approval and tx confirmation
 */
export function useTxWaitingLogicExplicit(
  txCreatingFunction: TTxCreatingFunction,
  closeModal: () => void,
  setActiveTxHash: (val: string) => void,
  setWaitingForUserApproval: (val: boolean) => void,
  setWaitingForTxConfirmation: (val: boolean) => void,
  closeModalOnSuccess: boolean
) {
  const wrappedTxBuildingFunction = useCallback(async () => {
    let contractTx: ContractTransaction | undefined;

    // We are going to ask the user to approve tx
    setWaitingForUserApproval(true);

    try {
      contractTx = await txCreatingFunction();

      setActiveTxHash(contractTx?.hash || "");

      // We are after user approval, let's wait for the tx to be mined
      setWaitingForTxConfirmation(true);

      const receipt = await contractTx?.wait();
      console.log(receipt);

      if (closeModalOnSuccess) {
        closeModal();
      }
    } catch (e) {
      if (e.code === JSON_RPC_ERROR_CODES.provider.userRejectedRequest) {
        // User denied tx
        console.log("User denied tx");
      } else {
        // TODO : Have a more user-friendly error communication.
        // DEV_NOTE :
        closeModal();
        throw e;
      }
    } finally {
      // No matter what, we ain't waiting after this line
      setWaitingForUserApproval(false);
      setWaitingForTxConfirmation(false);
    }
  }, [
    closeModal,
    closeModalOnSuccess,
    setActiveTxHash,
    setWaitingForTxConfirmation,
    setWaitingForUserApproval,
    txCreatingFunction,
  ]);

  return wrappedTxBuildingFunction;
}
