import React, { useEffect } from "react";
import { MobXProviderContext } from "mobx-react";
import { IStores } from "./stores";
import { CryptoWalletConnectionStore } from "./CryptoWalletConnectionStore";
import { AccountStore } from "./AccountStore";
import { LendingNetworksStore } from "./LendingNetworksStore";
import { TLNContractsAddresses } from "../services/Services";
import { ModalsStore } from "./ModalsStore";
import { SingleCTokenStore } from "./multiInstanceStores/SingleCTokenStore";
import { SingleLNStore } from "./multiInstanceStores/SingleLNStore";
import { SystemStore } from "./SystemStore";

export function useStores(): IStores {
  // @ts-ignore
  return React.useContext(MobXProviderContext);
}

export function useCryptoWalletIntegrationStore(): CryptoWalletConnectionStore {
  return useStores().cryptoWalletIntegrationStore;
}

export function useAccountStore(): AccountStore {
  return useStores().accountStore;
}

export function useLendingNetworksStore(): LendingNetworksStore {
  return useStores().lendingNetworksStore;
}

export function useSystemStore(): SystemStore {
  return useStores().systemStore;
}

export function useModalsStore(): ModalsStore {
  return useStores().modalsStore;
}

export function useActiveLendingNetworkStore(): SingleLNStore | undefined {
  return useStores().lendingNetworksStore.activeLNStore;
}

export function useCTokenStoreByBaseAsset(
  baseAssetAddress: string
): SingleCTokenStore | undefined {
  const lendingNetworksStore = useLendingNetworksStore();
  const activeLN = lendingNetworksStore.activeLNStore;
  const cTokenStore = activeLN?.cTokensStores.find(
    (cTokenStore) => cTokenStore.underlyingAssetAddress === baseAssetAddress
  );
  return cTokenStore;
}
