import React from "react";
import { Button, makeStyles } from "@material-ui/core";

interface IProps {
  text: string;
  onClick: () => void;
  disabled?: boolean;
}

const useStyles = makeStyles((theme) => ({}));

export const ModalActionButton = React.memo<IProps>((props) => {
  const classes = useStyles();
  const { text, onClick, disabled } = props;

  console.log("Building button");

  return (
    <Button
      variant={"contained"}
      fullWidth
      disabled={disabled}
      onClick={onClick}
      color={"primary"}
    >
      {text}
    </Button>
  );
});
