import { Signer } from "@ethersproject/abstract-signer";
import { Provider } from "@ethersproject/abstract-provider";
import { IErc20BuilderService } from "./IErc20BuilderService";
import { IErc20Service } from "./IErc20Service";
import { Erc20Service } from "./Erc20Service";

export class Erc20BuilderService implements IErc20BuilderService {
  constructor(private signerOrProvider: Signer | Provider) {}

  buildErc20Service(erc20Address: string): IErc20Service {
    return new Erc20Service(erc20Address, this.signerOrProvider);
  }
}
