import React from "react";
import {
  Collapse,
  Fade,
  Grow,
  makeStyles,
  Typography,
} from "@material-ui/core";
import clsx from "clsx";
import { TokenIcon } from "../../../../icons/TokenIcon";
import { AnimatedNumberText } from "../../../../AnimatedNumberText/AnimatedNumberText";

export type TRatesRow = {
  label: string;
  value: number;
  valuePrefix?: string;
  valueSuffix?: string;
  tokenSymbol?: string;

  // Predicted value
  displayPredictedValues?: boolean;
  predictedValue?: number;
};

interface IProps {
  title: string;
  subTitle?: string;

  rateRows: TRatesRow[];

  // Styling
  bottomBorder?: boolean;
}

const GRAYISH_TEXT = "#AAB8C1";

const useStyles = makeStyles((theme) => ({
  container: {
    width: "100%",
    display: "flex",
    flexDirection: "column",
    // alignItems: "stretch",
  },

  rateRow: {
    width: "100%",
    display: "flex",
    flexDirection: "row",
    justifyContent: "start",
    alignItems: "center",
    padding: "0.5rem",
  },

  bottomBorder: {
    borderBottom: "1px solid #CCD6DD",
  },
  heavyBottomBorder: {
    borderBottom: "1px solid rgb(20, 30, 39)",
  },
  symbolIcon: {
    marginRight: "0.25rem",
  },
  labelText: {
    // Push to the right
    color: GRAYISH_TEXT,
  },
  valueText: {
    // Push to the right
    // marginLeft: "auto",
    color: theme.palette.text.primary,
  },
}));

export const BaseRates = React.memo<IProps>((props) => {
  const classes = useStyles();
  const { title, subTitle, rateRows, bottomBorder } = props;

  const arrowText = "->";

  return (
    <div
      className={clsx([
        classes.container,
        bottomBorder ? classes.heavyBottomBorder : null,
      ])}
    >
      <Typography>{title}</Typography>
      {subTitle && <Typography variant={"caption"}>{subTitle}</Typography>}
      {rateRows.map((rateRow, index) => {
        const isLast = index === rateRows.length;
        const predictedValue = rateRow?.predictedValue || 0;

        return (
          <div
            className={clsx([
              classes.rateRow,
              isLast ? null : classes.bottomBorder,
            ])}
            key={`${rateRow.tokenSymbol}_${rateRow.label}`}
          >
            {rateRow.tokenSymbol && (
              <TokenIcon
                symbol={rateRow.tokenSymbol}
                extraClasses={[classes.symbolIcon]}
              />
            )}
            {/* Label */}
            <Typography className={classes.labelText}>
              {rateRow.label}
            </Typography>
            <div></div>
            <Typography
              className={classes.valueText}
              style={{ marginLeft: "auto" }}
            >
              {(+rateRow.value.toFixed(2)).toLocaleString()}
              {"  "}
              {!rateRow.displayPredictedValues && rateRow.valueSuffix}
            </Typography>

            {rateRow.displayPredictedValues && (
              <>
                <Typography className={classes.valueText}>
                  {"   "}
                  {arrowText}
                  {/*{rateRow.valuePrefix}*/}
                  {(+predictedValue.toFixed(2)).toLocaleString()}
                  {"  "}
                  {rateRow.valueSuffix}
                </Typography>
              </>
            )}
          </div>
        );
      })}
    </div>
  );
});
