import { configure } from "mobx";
import { IStores } from "./stores";

import { CryptoWalletConnectionStore } from "./CryptoWalletConnectionStore";

// This import ensures mobx batching
// TODO : Check if still relevant to mobx6
// import "mobx-react-lite/batchingForReactDom";
import { ICryptoWalletConnectionService } from "../services/cryptoWalletConnectionService/ICryptoWalletConnectionService";
import { ILendingNetworksService } from "../services/lendingNetworksService/ILendingNetworksService";
import { AccountStore } from "./AccountStore";
import { LendingNetworksStore } from "./LendingNetworksStore";
import { ModalsStore } from "./ModalsStore";
import { IErc20BuilderService } from "../services/contractsIntegration/erc20Service/IErc20BuilderService";
import { ICTokensBuilderService } from "../services/contractsIntegration/cTokensService/ICTokensBuilderService";
import { SystemStore } from "./SystemStore";

/**
 * Builds and initializes all of the stores
 */
export function getStores(
  cryptoWalletConnectionService: ICryptoWalletConnectionService,
  lendingNetworksService: ILendingNetworksService,
  erc20BuilderService: IErc20BuilderService,
  cTokensBuilderService: ICTokensBuilderService
): IStores {
  // Create stores instances + Hydrate the stores
  const cryptoWalletIntegrationStore = new CryptoWalletConnectionStore(
    cryptoWalletConnectionService
  );

  const systemStore = new SystemStore(cryptoWalletIntegrationStore);

  const lendingNetworksStore = new LendingNetworksStore(
    cryptoWalletIntegrationStore,
    systemStore,
    lendingNetworksService
  );

  const accountStore = new AccountStore(
    cryptoWalletIntegrationStore,
    systemStore,
    lendingNetworksStore,
    erc20BuilderService,
    cTokensBuilderService
  );

  const stores: IStores = {
    cryptoWalletIntegrationStore,
    accountStore,
    lendingNetworksStore,
    systemStore,
    modalsStore: new ModalsStore(),
  };

  return stores;
}

// TODO : Check if this is still relevant to mobx6
/**
 * Configures the mobx library. Should get called at App's initialization.
 */
export function configureMobx() {
  configure({
    enforceActions: "observed",
  });
}
