import React, { FunctionComponent, SFC } from "react";
import { Grid, makeStyles } from "@material-ui/core";

import clsx from "clsx";
import { TAssetInMarketGist, TMarketType } from "./types";
import { useCommonClasses } from "../../../../theme/commonClasses";
import { Panel } from "../../../../components/panel/Panel";
import { AssetPanelRow } from "./AssetPanelRow";
import { TAssetInMarketParameters } from "../../../../services/lendingNetworksService/ILendingNetworksService";
import { TCTokenMetadata } from "../../../../services/contractsIntegration/CompoundLensService/ICompoundLensService";
import { TEXT_ON_LIGHT } from "../../../../theme/themeConstants";

interface IProps {
  header: string;
  marketType: TMarketType;
  assetsInMarket: TAssetInMarketParameters[];
  // Symbol -> balanceInUnits
  userBalancesInUnits: Record<string, number>;
  marketsUserIsIn: string[];

  /**
   *  Asset 'collateral' flag toggle is clicked (user want to add/remove asset from being used as collateral)
   */
  onCollateralableChange: (
    baseAssetAddress: string,
    newCollaterableValue: boolean
  ) => void;

  /**
   *  Asset row is clicked (user want to interact with this asset's market)
   */
  onAssetSelected: (baseAssetAddress: string) => void;
}

const useStyles = makeStyles((theme) => ({
  panelHeader: {
    display: "flex",
    flexFlow: "inherit",
    alignItems: "center",
    justifyContent: "space-between",
    fontSize: "1.1rem",
    padding: "1rem 1.75rem",
    borderBottom: "1px solid rgba(0, 0, 0, 0.05)",
    color: TEXT_ON_LIGHT,
  },
  zeroMargin: {
    margin: 0,
  },
  assetsList: {},
  panelLabels: {
    // display: "flex",
    padding: "1rem 1.75rem",
    borderBottom: "1px solid rgba(0, 0, 0, 0.05)",
    alignItems: "center",
  },
  assets: {},
}));

// export const MarketsPanel = React.memo<IProps>((props) => {
export const MarketsPanel: FunctionComponent<IProps> = (props) => {
  const classes = useStyles();
  const commonClasses = useCommonClasses();
  const {
    header,
    marketType,
    assetsInMarket,
    userBalancesInUnits,
    marketsUserIsIn,
    onCollateralableChange,
    onAssetSelected,
  } = props;

  console.log(`Building markets panel ${header}`);

  return (
    <Panel>
      <div className={classes.panelHeader}>
        <h4 className={classes.zeroMargin}>{header}</h4>
      </div>
      <div className={classes.assetsList}>
        <Grid container direction={"row"} className={classes.panelLabels}>
          {/* Asset */}
          <Grid item xs={4} sm={4}>
            <label className={commonClasses.label}>Asset</label>
          </Grid>

          {/* APY */}
          <Grid
            className={clsx([
              commonClasses.textCenter,
              commonClasses.mobileHide,
            ])}
            item
            xs={false}
            sm={3}
          >
            <label className={clsx([commonClasses.label])}>APY</label>
          </Grid>

          {/* WALLET */}
          <Grid className={commonClasses.textCenter} item xs={4} sm={3}>
            <label
              className={clsx([commonClasses.label, commonClasses.textRight])}
            >
              Wallet
            </label>
          </Grid>

          {/* Collateral/Liquidity */}
          <Grid className={commonClasses.textRight} item xs={4} sm={2}>
            <label
              className={clsx([commonClasses.label, commonClasses.textRight])}
            >
              {marketType === "supply" ? "Collateral" : "Liquidity"}
            </label>
          </Grid>
        </Grid>

        <div className={classes.assets}>
          {assetsInMarket.map((asset) => (
            <AssetPanelRow
              marketType={marketType}
              assetSummary={asset}
              unitsInWallet={userBalancesInUnits[asset.symbol] || 0}
              key={asset.symbol}
              onCollateralableChange={onCollateralableChange}
              onAssetSelected={onAssetSelected}
            />
          ))}
        </div>
      </div>
    </Panel>
  );
};
