import React, {
  ReactNode,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from "react";
import {
  IMarketModalCommonStatePayload,
  IMarketModalPerTabStatePayload,
  IMarketModalStatePayload,
  MarketInteractionModal,
} from "./base/MarketInteractionModal";
import { Input } from "@material-ui/core";
import { observer } from "mobx-react";
import { BigNumber, ethers } from "ethers";
import {
  useCalculateChangeInBorrowBalance,
  useMarketInteractionActiveTabState,
  useMarketInteractionModalBase,
  useMarketInteractionModalTabsState,
  useOnEnableClicked,
} from "./marketInteractionModalHooks";
import { MainModalMessage } from "./base/mainModalMessage/MainModalMessage";
import {
  useTxWaitingLogicExplicit,
  useTxWaitingStateExplicit,
} from "../txWaitingModal/txWaitingHooks";
import { TModalFooterMessagePayload } from "./base/ModalFooter";
import { useSystemStore } from "../../../store/storeHooks";

interface IProps {
  open: boolean;
  closeModal: () => void;
}

const ONATIVE_MAX_REPAY_FACTOR = 1.05;
// Percentage in factor from max limit that is considered safe.
const SAFE_BORROW_FACTOR = 0.4;

export const BorrowModal = observer<React.FunctionComponent<IProps>>(
  (props) => {
    const { open, closeModal } = props;

    const systemStore = useSystemStore();

    const {
      lendingNetworksStore,
      cTokenStore,
      modalsStore,
      accountStore,
      assetBaseAddress,
      maximillionAddress,
      activeLNStore,
      isNativeCoin,
      cToken,
      isCollaterable,

      walletBalance,
      needToApprove,
      hasWalletBalance,

      collateralPotentialInUSD,
      distributionTokenSymbol,
      supplyTokenAddress,
      supplyTokenSymbol,
      supplyTokenName,
      totalAccountBorrowBalanceUsd,
      usedBorrowPercentage,
      collateralFactorInUnits,
      priceCalculationDecimals,
      // currentBorrowLimitUsd,
      pricePerUnitUsd,
      distributionAssetApy,
      borrowApy,
      supplyApy,

      liquidityInAssetUnits,
      hasAvailableLiquidity,
    } = useMarketInteractionModalBase();

    const {
      activeTxHash,
      setActiveTxHash,
      waitingForUserApproval,
      waitingForTxConfirmation,
      setWaitingForUserApproval,
      setWaitingForTxConfirmation,
    } = useTxWaitingStateExplicit();

    const {
      activeTabIndex,
      setActiveTabIndex,
    } = useMarketInteractionModalTabsState();

    const [isBorrow, isRepay] = useMarketInteractionActiveTabState(
      activeTabIndex
    );

    const onEnableClicked = useOnEnableClicked(
      cToken,
      assetBaseAddress,
      closeModal,
      setActiveTxHash,
      setWaitingForUserApproval,
      setWaitingForTxConfirmation
    );

    const [isRepayingMax, setIsRepayingMax] = useState(false);
    const [borrowInputValue, setBorrowInputValue] = useState(0);
    const [repayInputValue, setRepayInputValue] = useState(0);

    useEffect(() => {
      let newToken = cToken;
      setIsRepayingMax(false);
    }, [cToken]);

    const clearStateBeforeClose = useCallback(() => {
      setBorrowInputValue(0);
      setRepayInputValue(0);
      closeModal();
    }, [closeModal]);

    // Constants
    const tab0Title = "Borrow";
    const tab1Title = "Repay";

    // TODO : Separate inputs for each usage / make a component
    const inputComponent = useMemo(() => {
      return (
        <div
          style={{
            width: "100%",
            padding: "2.5rem",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Input
            style={{ textAlign: "center" }}
            inputProps={{ style: { textAlign: "center" } }}
            type={"number"}
            title={"Input"}
            name={"Input"}
            value={isBorrow ? borrowInputValue : repayInputValue}
            onChange={(e) =>
              isBorrow
                ? setBorrowInputValue(sanitizeNumericInput(e.target.value))
                : setRepayInputValue(sanitizeNumericInput(e.target.value))
            }
          />
        </div>
      );
    }, [borrowInputValue, isBorrow, repayInputValue]);

    // TODO : CRITICAL : Add test for 'some' approval (not unlimitted) as well
    const hasApprovedERC20 = cTokenStore?.hasAnyAllowance;
    // Is our user borrowing this asset ?
    const currentBorrowBalance = cTokenStore?.accountBorrowInUnits || 0;
    const hasBorrowBalance = currentBorrowBalance > 0;
    const needsToButNotYetApproved = needToApprove && !hasApprovedERC20;

    const createBorrowTx = () =>
      accountStore.borrowFromMarket(
        cToken,
        ethers.utils.parseUnits(
          borrowInputValue.toString(),
          cTokenStore?.underlyingDecimals || -1
        ),
        isNativeCoin,
        maximillionAddress
      );

    const repayAmount = useMemo(() => {
      if (isRepayingMax) {
        if (isNativeCoin) {
          // TODO : Go over this calculation and maybe use fixed decimals/from chain store + put the factor somewhere proper
          return ethers.utils.parseUnits(
            (currentBorrowBalance * ONATIVE_MAX_REPAY_FACTOR).toString(),
            cTokenStore?.underlyingDecimals || 0
          );
        } else {
          return ethers.constants.MaxUint256;
        }
      } else {
        return ethers.utils.parseUnits(
          repayInputValue.toString(),
          cTokenStore?.underlyingDecimals || 0
        );
      }
    }, [
      cTokenStore?.underlyingDecimals,
      currentBorrowBalance,
      isNativeCoin,
      isRepayingMax,
      repayInputValue,
    ]);

    const createRepayTx = () =>
      accountStore.repayToMarket(
        cToken,
        repayAmount,
        isNativeCoin,
        maximillionAddress
      );

    const wrappedBorrowTx = useTxWaitingLogicExplicit(
      createBorrowTx,
      closeModal,
      setActiveTxHash,
      setWaitingForUserApproval,
      setWaitingForTxConfirmation,
      true
    );

    const wrappedRepayTx = useTxWaitingLogicExplicit(
      createRepayTx,
      closeModal,
      setActiveTxHash,
      setWaitingForUserApproval,
      setWaitingForTxConfirmation,
      true
    );

    const onBorrowClicked = useCallback(async () => {
      if (!cToken) {
        return console.log("No CToken Address");
      }

      console.log(`Borrow button clicked`);
      await wrappedBorrowTx();
      console.log(`Borrow is done !`);
    }, [cToken, wrappedBorrowTx]);
    const onBorrowMaxSafeClicked = useCallback(async () => {
      if (!cToken) {
        return console.log("No CToken Address");
      }

      const safeAmountInUsd = collateralPotentialInUSD * SAFE_BORROW_FACTOR;
      const amountLeftToSafe = safeAmountInUsd - totalAccountBorrowBalanceUsd;

      if (amountLeftToSafe < 0) {
        console.log(
          `Already over safe borrow amount : safe is : ${safeAmountInUsd} | borrowedUsd is ${totalAccountBorrowBalanceUsd}`
        );
        setBorrowInputValue(0);
      } else {
        const safeBorrowInUnits =
          cTokenStore?.getUnitsForUsdValue(amountLeftToSafe) || 0;

        // TODO : CRITICAL : Move to constants
        // DEV_NOTE : Decided to round up to 6 ( the markets decimals are 8 - this leaves us some space + this is the lowest decimals for all base assets - so we can safely use it)
        setBorrowInputValue(parseFloat(safeBorrowInUnits.toFixed(6)));
      }
    }, [
      cToken,
      cTokenStore,
      collateralPotentialInUSD,
      totalAccountBorrowBalanceUsd,
    ]);

    const onRepayClicked = useCallback(async () => {
      console.log(`Repay button clicked`);
      await wrappedRepayTx();
      console.log(`Repay is done !`);
    }, [wrappedRepayTx]);

    // How many units does the cToken holds ?
    const cTokenCashInUnits = cTokenStore?.cTokenCashInUnits || 0;

    // Is borrowing from this asset ?
    const accountBorrowInUnits = cTokenStore?.accountBorrowInUnits || 0;

    // How much underlying (in USD) does the market hold ?
    const cTokenCashInUsd = cTokenStore?.cTokenCashInUSD || 0;

    const repayIsZero = repayInputValue === 0;
    const hasEnoughToRepayAll = walletBalance > accountBorrowInUnits;
    const canRepayMax = hasWalletBalance && hasEnoughToRepayAll && !repayIsZero;

    const onSetRepayToMaxClicked = useCallback(() => {
      console.log("set repay max");
      setIsRepayingMax(!isRepayingMax);
    }, [isRepayingMax]);

    const supplyTokenExplorerLink = `${systemStore.explorerBaseAddressUrl}/${supplyTokenAddress}`;
    console.log({ supplyTokenExplorerLink });

    const messageOrInputComponent = useMemo<ReactNode>(() => {
      if (isRepay) {
        if (needsToButNotYetApproved) {
          // Message component - needs to approve
          return (
            <MainModalMessage
              tokenSymbol={supplyTokenSymbol}
              message={`Please approve ${supplyTokenSymbol} usage`}
              explorerLink={supplyTokenExplorerLink}
            />
          );
        } else if (isRepayingMax) {
          return (
            <MainModalMessage
              tokenSymbol={supplyTokenSymbol}
              message={`Repaying all (~${(+currentBorrowBalance.toFixed(
                2
              )).toLocaleString()})`}
              explorerLink={supplyTokenExplorerLink}
            />
          );
        } else {
          // Input component - how much to supply ?
          return inputComponent;
        }
      } else if (isBorrow) {
        // Input component - how much to withdraw ?
        return inputComponent;
      }

      // Safe error
      return <></>;
    }, [
      currentBorrowBalance,
      inputComponent,
      isBorrow,
      isRepay,
      isRepayingMax,
      needsToButNotYetApproved,
      supplyTokenExplorerLink,
      supplyTokenSymbol,
    ]);

    const footerMessagePayloads = useMemo<TModalFooterMessagePayload[]>(() => {
      return [
        {
          title: `${supplyTokenSymbol} Price`,
          tokenAmount: pricePerUnitUsd,
          tokenSymbol: "$",
        },
        {
          title: "Currently Borrowing",
          tokenAmount: accountBorrowInUnits,
          tokenSymbol: supplyTokenSymbol,
        },
        {
          title: "Wallet Balance",
          tokenAmount: walletBalance,
          tokenSymbol: supplyTokenSymbol,
        },
      ];
    }, [
      accountBorrowInUnits,
      pricePerUnitUsd,
      supplyTokenSymbol,
      walletBalance,
    ]);

    // // Must have active borrow in this asset + Must approve (if needed)
    // const canRepay =
    //   hasBorrowBalance && (needToApprove ? hasApprovedERC20 : true);

    // TODO : Make sure this is a right assumption
    // Either what the user is allowed to borrow, or everything there is.
    const maxPossibleBorrow = Math.max(
      liquidityInAssetUnits,
      cTokenCashInUnits
    );

    // Common payload part
    const commonPayloadPart = useMemo<IMarketModalCommonStatePayload>(() => {
      const commonModalState: IMarketModalCommonStatePayload = {
        // Tabs
        currentTabIndex: activeTabIndex,
        onTabIndexChange: setActiveTabIndex,
        tab0Title,
        tab1Title,

        // Assets
        actionAssetSymbol: supplyTokenSymbol,
        actionAssetName: supplyTokenName,
        distributionAssetSymbol: distributionTokenSymbol,
        assetExplorerLink: supplyTokenExplorerLink,

        // Borrow Limit (optional)
        // DEV_NOTE : This was a quick-fix, so the names were not changed. What
        //            we want to show is the borrow balance (not limit)
        currentBorrowLimit: totalAccountBorrowBalanceUsd,
        usedBorrowLimitPercentage: usedBorrowPercentage,

        // Tx Waiting
        explorerName: systemStore.explorerName,
        explorerUrlBaseForTx: systemStore.explorerBaseTxUrl,
        activeTxHash,
        waitingForUserApproval,
        waitingForTxConfirmation,
      };

      return commonModalState;
    }, [
      activeTabIndex,
      activeTxHash,
      distributionTokenSymbol,
      setActiveTabIndex,
      supplyTokenExplorerLink,
      supplyTokenName,
      supplyTokenSymbol,
      systemStore.explorerBaseTxUrl,
      systemStore.explorerName,
      totalAccountBorrowBalanceUsd,
      usedBorrowPercentage,
      waitingForTxConfirmation,
      waitingForUserApproval,
    ]);

    // Payload for Borrow
    const marketModalStatePayloadForBorrowTab = useMemo<IMarketModalPerTabStatePayload>(() => {
      // Calculate the changes to borrow limit (USD and % used)
      const {
        actionLeadsToShortfall,
        actionEffectInUsd,
        usedBorrowLimitPercentageAfterChange,
        borrowBalanceAfterChange,
      } = useCalculateChangeInBorrowBalance(
        collateralPotentialInUSD,
        totalAccountBorrowBalanceUsd,
        borrowInputValue, // Borrowing more will increase the borrow balance
        pricePerUnitUsd,
        priceCalculationDecimals
      );

      const isRequestingMoreThanCanBorrow =
        borrowInputValue > maxPossibleBorrow;

      const borrowUsdSum = actionEffectInUsd;
      const isRequestingMoreThanMarketLiquidity =
        borrowUsdSum > cTokenCashInUsd;

      const canBorrow =
        hasAvailableLiquidity &&
        !isRequestingMoreThanCanBorrow &&
        !isRequestingMoreThanMarketLiquidity &&
        !actionLeadsToShortfall;

      let reasonForNotBeingAbleToBorrow = undefined;
      if (!hasAvailableLiquidity) {
        reasonForNotBeingAbleToBorrow = "Borrowing limit reached";
      } else if (isRequestingMoreThanCanBorrow) {
        reasonForNotBeingAbleToBorrow = "Requesting more than can borrow";
      } else if (isRequestingMoreThanMarketLiquidity) {
        reasonForNotBeingAbleToBorrow = "Exceeding market liquidity";
      } else if (actionLeadsToShortfall) {
        reasonForNotBeingAbleToBorrow = "Leads to shortfall";
      }

      const actionButtonTextForBorrow = "Borrow";
      const actionButtonDisabledText = reasonForNotBeingAbleToBorrow;

      const activeButtonActive = canBorrow && borrowInputValue > 0;

      const actionButtonCbForSupply = onBorrowClicked;

      const showPredictedBorrowLimits = actionEffectInUsd !== 0;

      const canBorrowMoreSafely =
        usedBorrowPercentage < SAFE_BORROW_FACTOR * 100;

      const payload: IMarketModalPerTabStatePayload = {
        // Message or input component
        messageOrInput: messageOrInputComponent,
        inputActionButtonText: "Max Safe",
        showInputActionButton: true,
        inputActionButtonActive: canBorrowMoreSafely,
        onInputActionButtonPressed: onBorrowMaxSafeClicked,

        // Action Rates
        actionName: "Borrow",
        actionApy: borrowApy,
        distributionApy: distributionAssetApy,

        // Borrow Limit (optional)
        showBorrowLimit: true,
        borrowLimitAfterChange: borrowBalanceAfterChange,
        usedBorrowLimitPercentageAfterChange: usedBorrowLimitPercentageAfterChange,
        showPredictedBorrowLimits,

        // Action button
        actionButtonActive: activeButtonActive,
        actionButtonText: actionButtonTextForBorrow,
        actionButtonDisabledText,
        actionButtonOnClick: actionButtonCbForSupply,

        // Footer
        footerMessagesPayloads: footerMessagePayloads,
      };

      return payload;
    }, [
      borrowApy,
      borrowInputValue,
      cTokenCashInUsd,
      collateralPotentialInUSD,
      distributionAssetApy,
      footerMessagePayloads,
      hasAvailableLiquidity,
      maxPossibleBorrow,
      messageOrInputComponent,
      onBorrowClicked,
      onBorrowMaxSafeClicked,
      priceCalculationDecimals,
      pricePerUnitUsd,
      totalAccountBorrowBalanceUsd,
      usedBorrowPercentage,
    ]);

    // Payload for repay
    const marketModalStatePayloadForRepayTab = useMemo<IMarketModalPerTabStatePayload>(() => {
      const isApproveDoneWith = needToApprove ? hasApprovedERC20 : true;

      const isOfferingMoreThanWalletBalance = repayInputValue > walletBalance;
      const isOfferingMoreThanHasBorrowed =
        repayInputValue > accountBorrowInUnits;

      // Must have enough wallet balance in this asset + Must approve (if needed)
      const canRepay =
        (hasWalletBalance &&
          !isOfferingMoreThanWalletBalance &&
          !isOfferingMoreThanHasBorrowed &&
          !repayIsZero &&
          isApproveDoneWith) ||
        isRepayingMax;

      const actionButtonActive = canRepay || needsToButNotYetApproved;
      const actionButtonCbForRepay = needsToButNotYetApproved
        ? onEnableClicked
        : onRepayClicked;

      let reasonForNotBeingAbleToRepay = undefined;
      if (isOfferingMoreThanWalletBalance) {
        reasonForNotBeingAbleToRepay = "Exceeding wallet balance";
      } else if (isOfferingMoreThanHasBorrowed) {
        reasonForNotBeingAbleToRepay = "Exceeding borrowed amount";
      } else if (repayIsZero) {
        // DEV_NOTE : Will be disabled with the call to action
        reasonForNotBeingAbleToRepay = "Repay";
      }

      const actionButtonTextForRepay = canRepay
        ? "Repay"
        : needToApprove
        ? "Approve"
        : "";

      const actionButtonDisabledText = reasonForNotBeingAbleToRepay;

      // Calculate borrow limit (USD and % used) after requested withdraw
      const {
        usedBorrowLimitPercentageAfterChange,
        actionEffectInUsd,
        actionLeadsToShortfall,
        borrowBalanceAfterChange,
      } = useCalculateChangeInBorrowBalance(
        collateralPotentialInUSD,
        totalAccountBorrowBalanceUsd,
        repayInputValue * -1, // Repaying will decrease the borrow balance
        pricePerUnitUsd,
        priceCalculationDecimals
      );

      const showPredictedBorrowLimits = actionEffectInUsd !== 0;

      const inputActionButtonText = isRepayingMax ? "Cancel" : "All";
      const showInputActionButton =
        (isRepay || false) && !needsToButNotYetApproved && hasBorrowBalance;
      const inputActionButtonActive = hasEnoughToRepayAll;
      const onInputActionButtonPressed = onSetRepayToMaxClicked;

      const payload: IMarketModalPerTabStatePayload = {
        // Message or input component
        messageOrInput: messageOrInputComponent,
        inputActionButtonText,
        showInputActionButton,
        onInputActionButtonPressed,
        inputActionButtonActive,

        // Action Rates
        actionName: "Supply",
        actionApy: 4.56,
        distributionApy: distributionAssetApy,

        // Borrow Limit (optional)
        showBorrowLimit: showPredictedBorrowLimits,
        borrowLimitAfterChange: borrowBalanceAfterChange,
        showPredictedBorrowLimits,
        usedBorrowLimitPercentageAfterChange,

        // Action button
        actionButtonActive: !!actionButtonActive,
        actionButtonText: actionButtonTextForRepay,
        actionButtonDisabledText,
        actionButtonOnClick: actionButtonCbForRepay,

        // Footer
        footerMessagesPayloads: footerMessagePayloads,
      };

      return payload;
    }, [
      accountBorrowInUnits,
      collateralPotentialInUSD,
      distributionAssetApy,
      footerMessagePayloads,
      hasApprovedERC20,
      hasBorrowBalance,
      hasEnoughToRepayAll,
      hasWalletBalance,
      isRepay,
      isRepayingMax,
      messageOrInputComponent,
      needToApprove,
      needsToButNotYetApproved,
      onEnableClicked,
      onRepayClicked,
      onSetRepayToMaxClicked,
      priceCalculationDecimals,
      pricePerUnitUsd,
      repayInputValue,
      repayIsZero,
      totalAccountBorrowBalanceUsd,
      walletBalance,
    ]);

    const statePerTabPayload = isBorrow
      ? marketModalStatePayloadForBorrowTab
      : marketModalStatePayloadForRepayTab;

    const marketModalStatePayload = useMemo<IMarketModalStatePayload>(() => {
      const payload: IMarketModalStatePayload = {
        ...commonPayloadPart,
        ...statePerTabPayload,
      };

      return payload;
    }, [commonPayloadPart, statePerTabPayload]);

    return (
      <MarketInteractionModal
        open={open}
        closeModal={clearStateBeforeClose}
        {...marketModalStatePayload}
      />
    );
  }
);

function sanitizeNumericInput(stringValueOfNumericInput?: string): number {
  let safeString = stringValueOfNumericInput || "0";
  // DEV_NOTE : Barbaric way
  while (safeString.length > 1 && safeString.charAt(0) === "0") {
    console.log(`Starts in 0 : ${safeString}`);
    safeString = safeString.substring(1, safeString.length);
    console.log(`After trim : ${safeString}`);
  }

  const parsedAsFloat = +safeString;
  if (!stringValueOfNumericInput || isNaN(parsedAsFloat)) {
    return 0;
  }

  return parsedAsFloat;
}
