import { ICryptoWalletConnectionService } from "./cryptoWalletConnectionService/ICryptoWalletConnectionService";
import { IEthereumProvider } from "./cryptoWalletConnectionService/IEthereumProvider";
import { CryptoWalletConnectionService } from "./cryptoWalletConnectionService/CryptoWalletConnectionService";
import { ILendingNetworksService } from "./lendingNetworksService/ILendingNetworksService";
import { LendingNetworksService } from "./lendingNetworksService/LendingNetworksService";
import { UniswapAnchoredViewService } from "./contractsIntegration/UniswapAnchoredView/UniswapAnchoredViewService";

import contractsAddresses from "../solidityInterfaces/addresses.json";
import { getSignerOrProvider } from "../ethereumConnection/ethereumConnection";
import { IComptrollerService } from "./contractsIntegration/ComptrollerService/IComptrollerService";
import { ComptrollerService } from "./contractsIntegration/ComptrollerService/ComptrollerService";
import { IErc20BuilderService } from "./contractsIntegration/erc20Service/IErc20BuilderService";
import { Erc20BuilderService } from "./contractsIntegration/erc20Service/Erc20BuilderService";
import { ICTokensBuilderService } from "./contractsIntegration/cTokensService/ICTokensBuilderService";
import { CTokensBuilderService } from "./contractsIntegration/cTokensService/CTokensBuilderService";

export interface IServices {
  cryptoWalletIntegrationService: ICryptoWalletConnectionService;
  lendingNetworksService: ILendingNetworksService;
  // DEV_NOTE : Seems no one is using this service
  uniswapAnchoredViewService: UniswapAnchoredViewService;

  // Service builders
  erc20ServiceBuilder: IErc20BuilderService;
  cTokenServiceBuilder: ICTokensBuilderService;
}

export type TLNContractsAddresses = {
  unitrollerAddress: string;
};

/**
 * Services that are LN specific and should be refreshed/replaced when switching a LN.
 */
export interface ILNServices {
  comptrollerLiveService: IComptrollerService;
}

// DEV_NOTE : For simplicity of early stage dev, we assume that we have ethereum provider, if not, we will not initialize the services.
export function buildServices(ethereumProvider: IEthereumProvider): IServices {
  const signerOrProvider = getSignerOrProvider(ethereumProvider);
  const addresses = getContractsAddresses();

  return {
    cryptoWalletIntegrationService: new CryptoWalletConnectionService(
      ethereumProvider
    ),
    lendingNetworksService: new LendingNetworksService(),
    uniswapAnchoredViewService: new UniswapAnchoredViewService(
      addresses.UniswapAnchoredView,
      signerOrProvider
    ),

    // Service builders
    erc20ServiceBuilder: new Erc20BuilderService(signerOrProvider),
    cTokenServiceBuilder: new CTokensBuilderService(signerOrProvider),
  };
}

// DEV_NOTE : For simplicity of early stage dev, we assume that we have ethereum provider, if not, we will not initialize the services.
export function buildLNServices(
  ethereumProvider: IEthereumProvider,
  contractsAddresses: TLNContractsAddresses
): ILNServices {
  const signerOrProvider = getSignerOrProvider(ethereumProvider);

  return {
    comptrollerLiveService: new ComptrollerService(
      contractsAddresses.unitrollerAddress,
      signerOrProvider
    ),
  };
}

