import { makeStyles, Paper } from "@material-ui/core";
import React from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import { LendingNetworkPage } from "./LendingNetworkPage/LendingNetworkPage";
import { AllNetworksPage } from "./AllNetworksPage/AllNetworksPage";

const useStyles = makeStyles(({ spacing }) => ({
  root: {
    borderRadius: 16,
    width: "80%",
    minHeight: 400,
    maxWidth: 1200,
    padding: spacing(2),
    marginTop: spacing(4),
    marginBottom: spacing(4),
    margin: "0 auto",
  },
}));

export const Main: React.FC = () => {
  const classes = useStyles();

  return (
    // <Paper elevation={22} className={classes.root}>
    <Switch>
      <Route path="/networks" exact component={AllNetworksPage} />
      <Route
        path="/networks/:network_id"
        render={({ match }) => (
          <LendingNetworkPage networkId={match.params.network_id} />
        )}
      />
      <Redirect to="/networks" />
    </Switch>
    // </Paper>
  );
};
