import {
  action,
  computed,
  IReactionDisposer,
  observable,
  reaction,
} from "mobx";
import { CryptoWalletConnectionStore } from "./CryptoWalletConnectionStore";
import { NATIVE_COIN_DETAILS_PER_CHAIN } from "../constants/supportedTokensPerChain";
import { TErc20Meta } from "../services/contractsIntegration/TokensLensService/ITokensLensService";
import { BigNumber } from "ethers";
import ChainAddresses from "../solidityInterfaces/addresses.json";
import { CompoundLensService } from "../services/contractsIntegration/CompoundLensService/CompoundLensService";
import {
  CHAIN_ID_ETHEREUM_ROPSTEN,
  LIVE_CHAIN_CONSTANTS,
  LIVE_CHAINS,
  LOCAL_CHAINS,
  TChainExplorerBaseUrls,
  TChainIds,
  TLiveChainIds,
} from "../constants/chainConstants";

export type TTokenMetaWithBalance = TErc20Meta & {
  balance: BigNumber;
  name: string;
  balanceInUnits: number;
};

export type TAssetAmount = {
  rawAmount: BigNumber;
  amountInUnits: number;
};

// TODO : Consider splitting some properties here to a standalone 'chainStore'
export class SystemStore {
  @observable public contractParametersDoneLoading = false;
  @observable public contractParametersErrorLoading = false;
  @observable public doneLoading = false;
  @observable public errorLoading = false;

  @observable public nativeCoinSymbol = "ETH";
  @observable public nativeCoinName = "Ether";
  @observable public nativeCoinDecimals = 18;
  @observable public compoundLensAddress = "";
  @observable public explorerName = "";
  @observable public explorerBaseUrl = "";
  @observable public explorerBaseTxUrl = "";
  @observable public explorerBaseBlockUrl = "";
  @observable public explorerBaseAddressUrl = "";

  // TODO : Move tx's state to a different store
  @observable
  public txPending = false;
  @observable public txHadError = false;
  @observable public txCanceled = false;

  @computed
  public get compoundLensServiceInitialized(): boolean {
    return this.compoundLensAddress !== "";
  }

  @computed
  public get compoundLensService(): CompoundLensService {
    if (this.compoundLensServiceInitialized) {
      return new CompoundLensService(
        this.compoundLensAddress,
        this.cryptoWalletIntegrationStore.signerOrProvider
      );
    } else {
      // @ts-ignore : We should always check for 'compoundLensServiceInitialized' before calling
      return null;
    }
  }

  private chainIdChangeReaction: IReactionDisposer;

  constructor(
    private cryptoWalletIntegrationStore: CryptoWalletConnectionStore
  ) {
    this.chainIdChangeReaction = reaction(
      () => [this.cryptoWalletIntegrationStore.chainId],
      async ([chainId]) => {
        this.setDoneLoading(false);
        await this.reactToConnectedChainChanged(chainId as TChainIds);
        this.setDoneLoading(true);
      },
      {
        fireImmediately: true,
      }
    );
  }

  // **** Computed values ****

  // **** Contract interactions ****

  // **** Current address changed ****

  private async reactToConnectedChainChanged(chainId: TChainIds) {
    console.log(`Chain ID ${chainId}`);
    // @ts-ignore
    console.log(
      `Native coin ${NATIVE_COIN_DETAILS_PER_CHAIN[chainId]?.symbol}`
    );

    // Update compound lens address
    // DEV_NOTE : This will not cause a re-render, but any call to the service will be to the right address
    // this.compoundLensServiceInstance.updateCompoundLensAddress(
    //   this.getChainCompoundLensAddress(chainId)
    // );

    // Compound lens address
    const chainCompoundLensAddress = this.getChainCompoundLensAddress(chainId);
    this.setCompoundLensAddress(chainCompoundLensAddress);

    // Chain explorer urls
    const chainExplorerBaseUrls = this.getChainExplorerUrls(chainId);
    this.setExplorerAddresses(chainExplorerBaseUrls);

    // Chain native coin
    const nativeCoinSymbol =
      NATIVE_COIN_DETAILS_PER_CHAIN[chainId]?.symbol || "ETH";
    const nativeCoinName =
      NATIVE_COIN_DETAILS_PER_CHAIN[chainId]?.name || "Ether";
    const nativeCoinDecimals =
      NATIVE_COIN_DETAILS_PER_CHAIN[chainId]?.decimals || 18;

    // @ts-ignore
    this.setNativeCoinDetails(
      nativeCoinSymbol,
      nativeCoinName,
      nativeCoinDecimals
    );
  }

  // **** Data reading and setting ****

  // **** Read and Set ****

  // ****  Subscriptions ****

  private async refreshAccountListeners(accountAddress: string) {
    this.cancelAllCurrentSubscriptions();
  }

  private cancelAllCurrentSubscriptions() {}

  private getChainCompoundLensAddress(chainId: TChainIds): string {
    console.log(`ChainId-lensAddress: ${chainId}`);
    if (LOCAL_CHAINS.includes(chainId)) {
      return ChainAddresses.CompoundLens;
    } else if (LIVE_CHAINS.includes(chainId)) {
      return LIVE_CHAIN_CONSTANTS[chainId as TLiveChainIds].compoundLensAddress;
    } else if ((chainId as number) === -1) {
      // DEV_NOTE : The initial value, not yet initialized.
      return "";
    } else {
      console.error(`Unsupported chain id of ${chainId} (${typeof chainId})`);
      return "";
    }
  }

  private getChainExplorerUrls(chainId: TChainIds): TChainExplorerBaseUrls {
    console.log(`ChainId-explorerAddress: ${chainId}`);
    const chainExplorerBaseUrls: TChainExplorerBaseUrls = {
      explorerName: "",
      addressUrlBase: "",
      baseUrl: "",
      blockUrlBase: "",
      txUrlBase: "",
    };

    if (LOCAL_CHAINS.includes(chainId)) {
      // DEV_NOTE : WE will returns the 'ropsten' urls here for dev purposes
      return LIVE_CHAIN_CONSTANTS[CHAIN_ID_ETHEREUM_ROPSTEN].explorerUrls;
    } else if (LIVE_CHAINS.includes(chainId)) {
      return LIVE_CHAIN_CONSTANTS[chainId as TLiveChainIds].explorerUrls;
    } else if ((chainId as number) === -1) {
      // DEV_NOTE : The initial value, not yet initialized.
      return chainExplorerBaseUrls;
    } else {
      console.error(`Unsupported chain id of ${chainId} (${typeof chainId})`);
      return chainExplorerBaseUrls;
    }
  }

  // ****  Complex setters ****
  private failLoadingProcess(error: Error) {
    this.setErrorLoading(true);
    this.setDoneLoading(true);
  }

  private resetTxIndicators() {
    this.setTxPending(false);
    this.setTxHadError(false);
    this.setTxCanceled(false);
  }

  // ****  Observables setter actions ****
  @action("setDoneLoading")
  private setDoneLoading(doneLoading: boolean) {
    this.doneLoading = doneLoading;
  }

  @action("setErrorLoading")
  private setErrorLoading(errorLoading: boolean) {
    this.errorLoading = errorLoading;
  }

  @action("setTxPending")
  private setTxPending(txPending: boolean) {
    this.txPending = txPending;
  }

  @action("setTxCanceled")
  private setTxCanceled(txCanceled: boolean) {
    this.txCanceled = txCanceled;
  }

  @action("setTxHadError")
  private setTxHadError(txHadError: boolean) {
    this.txHadError = txHadError;
  }

  @action("setNativeCoinDetails")
  private setNativeCoinDetails(
    nativeCoinSymbol: string,
    nativeCoinName: string,
    nativeCoinDecimals: number
  ) {
    console.log(`Native coin symbol ${nativeCoinSymbol} - ${nativeCoinName}`);
    this.nativeCoinSymbol = nativeCoinSymbol;
    this.nativeCoinName = nativeCoinName;
    this.nativeCoinDecimals = nativeCoinDecimals;
  }

  @action("setCompoundLensAddress")
  private setCompoundLensAddress(compoundLensAddress: string) {
    console.log(`Setting compoundLensAddress ${compoundLensAddress}`);
    this.compoundLensAddress = compoundLensAddress;
  }

  @action("setExplorerAddresses")
  private setExplorerAddresses(explorerAddresses: TChainExplorerBaseUrls) {
    console.log({ explorerAddresses });

    this.explorerName = explorerAddresses.explorerName;
    this.explorerBaseUrl = explorerAddresses.baseUrl;
    this.explorerBaseTxUrl = explorerAddresses.txUrlBase;
    this.explorerBaseAddressUrl = explorerAddresses.addressUrlBase;
    this.explorerBaseBlockUrl = explorerAddresses.blockUrlBase;
  }
}
