import { IEthereumProvider } from "../services/cryptoWalletConnectionService/IEthereumProvider";
import { Signer } from "@ethersproject/abstract-signer";
import { Provider } from "@ethersproject/abstract-provider";
import { ethers } from "ethers";
import { READ_ONLY_ETHEREUM_ENDPOINT } from "../configs";

export function getSignerOrProvider(
  ethereumProvider?: IEthereumProvider
): Signer | Provider {
  let signerOrProvider;

  // DEV_NOTE: This should allow us to continue reading
  if (ethereumProvider) {
    // @ts-ignore
    signerOrProvider = new ethers.providers.Web3Provider(ethereumProvider);
  } else {
    signerOrProvider = new ethers.providers.JsonRpcProvider(
      // TODO: Change this to network specific values (mainnet, kovan etc...)
      READ_ONLY_ETHEREUM_ENDPOINT
    );
  }

  const signer = signerOrProvider.getSigner();
  if (signer) {
    signer
      .getAddress()
      .then((address) => console.log(`Signer is ${address}`))
      .catch((e) => console.log(`Signer exists but we cannot get address`));
    signerOrProvider = signer;
  } else {
    console.log(`Provider only`);
  }

  return signerOrProvider;
}
