import {
  ILendingNetworkGist,
  ILendingNetworkParameters,
  ILendingNetworksService,
} from "../services/lendingNetworksService/ILendingNetworksService";
import { action, computed, makeObservable, observable } from "mobx";
import { SingleLNStore } from "./multiInstanceStores/SingleLNStore";
import { IObservableArray } from "mobx/dist/internal";
import { ICompoundLensService } from "../services/contractsIntegration/CompoundLensService/ICompoundLensService";
import { CryptoWalletConnectionStore } from "./CryptoWalletConnectionStore";
import { CompoundLensService } from "../services/contractsIntegration/CompoundLensService/CompoundLensService";
import { SystemStore } from "./SystemStore";

export class LendingNetworksStore {
  @observable isLoading: boolean = false;
  @observable
  lendingNetworkParameters: IObservableArray<ILendingNetworkParameters> = observable(
    []
  );
  @observable
  lendingNetworkGists: IObservableArray<ILendingNetworkGist> = observable([]);
  // @observable lendingNetworkLiveState: ILendingNetworkLiveState[] = [];
  @observable
  lendingNetworksStores: IObservableArray<SingleLNStore> = observable([]);

  @observable
  activeLendingNetworkId: string = "";

  @computed get allLNGists(): ILendingNetworkGist[] {
    const gists = this.lendingNetworksStores.map((lnStore) => lnStore.lnGist);
    return gists;
  }

  @computed get activeLNStore(): SingleLNStore | undefined {
    const activeLnStore = this.lendingNetworksStores.find(
      (lnStore) => lnStore.id === this.activeLendingNetworkId
    );

    return activeLnStore;
  }

  constructor(
    private cryptoWalletIntegrationStore: CryptoWalletConnectionStore,
    private systemStore: SystemStore,
    private lendingNetworksService: ILendingNetworksService
  ) {
    makeObservable(this);

    this.initializeStore();
  }

  public getLendingNetworkStore(networkId: string): SingleLNStore | null {
    return this.lendingNetworksStores.find((l) => l.id === networkId) ?? null;
  }

  async initializeStore() {
    this.setLoadingState(true);

    // Get params for all registered LNs
    await this.fetchAllLnParameters();

    // Create a store for each LN
    this.buildStoresForEachLn();

    // Set the current LN as the first (just to initialize it with something)
    // Initialize each store
    this.initializeLNStores();

    // this.setLendingNetworksGists(lnParameters);
    this.setLoadingState(false);
  }

  private async fetchAllLnParameters() {
    const lnParameters = await this.lendingNetworksService.fetchALlLendingNetworksParameters();
    this.setLendingNetworkParameters(lnParameters);
  }

  private buildStoresForEachLn() {
    const lnStores = this.lendingNetworkParameters.map((params) =>
      this.buildLNStore(params)
    );

    this.setLendingNetworksStores(lnStores);
  }

  /**
   * Calls 'initiailize' on each store
   */
  private initializeLNStores() {
    // TODO : Critical : Should not initialize al stores. only the ones that match the network
    this.lendingNetworksStores.forEach((lnStore) => lnStore.initialize());
  }

  private buildEmptyLnGist(
    lnParams: ILendingNetworkParameters
  ): ILendingNetworkGist {
    const gist: ILendingNetworkGist = {
      ...lnParams,
      tvl: 0,
      participatingSuppliers: 0,
    };

    return gist;
  }

  private buildLNStore(lnParams: ILendingNetworkParameters): SingleLNStore {
    // Start with an empty gist
    const gist: ILendingNetworkGist = {
      ...lnParams,
      tvl: 0,
      participatingSuppliers: 0,
    };

    // Build the store
    const lnStore = new SingleLNStore(
      this.cryptoWalletIntegrationStore,
      this.systemStore,
      this.lendingNetworksService,
      // this.compoundLensService,
      gist
    );

    return lnStore;
  }

  // **** Active LN ****
  public setActiveLn(lnIdAddress: string) {
    this.setActiveLendingNetworkId(lnIdAddress);
  }

  // **** Computed values ****

  // **** Contract interactions ****

  // **** Current address changed ****

  // **** Data reading and setting ****

  // **** Read and Set ****

  // ****  Subscriptions ****

  // ****  Complex setters ****

  // ****  Observables setter actions ****
  // @action("setLendingNetworksGists")
  // private setLendingNetworksGists(lendingNetworkGists: ILendingNetworkGist[]) {
  //   this.lendingNetworks = lendingNetworkGists.map(
  //     (gits) => new SingleLNStore(this.lendingNetworksService, gits)
  //   );
  // }

  @action("setLendingNetworksGists")
  private setLendingNetworksStores(lendingNetworkStores: SingleLNStore[]) {
    this.lendingNetworksStores.replace(lendingNetworkStores);
  }

  @action("setLendingNetworksGists")
  private setLendingNetworksGists(lendingNetworkGists: ILendingNetworkGist[]) {
    this.lendingNetworkGists.replace(lendingNetworkGists);
  }

  @action("setLendingNetworkParameters")
  private setLendingNetworkParameters(
    lendingNetworkParameters: ILendingNetworkParameters[]
  ) {
    this.lendingNetworkParameters.replace(lendingNetworkParameters);
  }

  @action("setActiveLendingNetworkId")
  private setActiveLendingNetworkId(activeLendingNetworkId: string) {
    this.activeLendingNetworkId = activeLendingNetworkId;
  }

  @action("setLoadingState")
  private setLoadingState(isLoading: boolean) {
    this.isLoading = isLoading;
  }
}
