import React, { useMemo } from "react";
import {
  Button,
  Fade,
  IconButton,
  makeStyles,
  Modal,
  Typography,
  useTheme,
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";
import {
  BRAND_MAIN_COLOR,
  GRAYISH_TEXT,
  TEXT_ON_LIGHT,
} from "../../../theme/themeConstants";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";

export type TTxWaitingStage = "USER_APPROVAL" | "CONFIRMATION";

interface IProps {
  open: boolean;
  closeModal: () => void;

  txWaitingStage: TTxWaitingStage;
  txHash?: string;
  explorerUrlBaseForTx?: string;
  explorerName?: string;
}

const TOP_DISTANCE_REM = 0.5;

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    maxWidth: "90%",
    marginRight: "auto",
    marginLeft: "auto",
  },
  txWaitingPanel: {
    position: "relative",
    width: "30rem",
    maxWidth: "100%",
    backgroundColor: "#F9FAFB",
  },
  panelHeader: {
    width: "100%",
    // color: "#141E27",
    color: TEXT_ON_LIGHT,
    display: "flex",
    flexDirection: "row",
    justifyContent: "center",
    alignItems: "center",
    borderBottom: "1px solid rgba(0, 0, 0, 0.05)",

    padding: `${TOP_DISTANCE_REM * 2}rem`,

    // Mobile
    [theme.breakpoints.down("sm")]: {
      // padding: "1.0rem",
      // display: "flex",
      // flexDirection: "row",
      // alignItems: "center",
    },
  },
  title: {
    fontWeight: "bold",
  },
  tokenIcon: {
    marginRight: "0.75rem",
    width: "2.5rem",
    height: "2.5rem",
    position: "fixed",
    left: 0,
  },
  upperRightCorner: {
    position: "absolute",
    right: `${TOP_DISTANCE_REM / 2}rem`,
    top: `${TOP_DISTANCE_REM / 2}rem`,
    // Mobile
    [theme.breakpoints.down("sm")]: {
      right: `${TOP_DISTANCE_REM / 8}rem`,
      top: `${TOP_DISTANCE_REM / 8}rem`,
    },
  },
  upperLeftCorner: {
    position: "absolute",
    left: `${TOP_DISTANCE_REM}rem`,
    top: `${TOP_DISTANCE_REM}rem`,
  },

  messageContainer: {
    // paddingTop: "2rem",
    paddingBottom: "1rem",
    paddingLeft: "1.5rem",
    paddingRight: "1.5rem",
    textAlign: "center",

    color: GRAYISH_TEXT,
  },
  explorerLink: {
    // color: theme.palette.primary.main,
    color: GRAYISH_TEXT,

    "&:hover": {
      color: theme.palette.primary.main,
    },
  },
}));

export const TxWaitingModal = React.memo<IProps>((props) => {
  const classes = useStyles();
  const {
    open,
    closeModal,
    txWaitingStage,
    txHash,
    explorerUrlBaseForTx,
    explorerName,
  } = props;
  const theme = useTheme();

  const isWaitingForUserApproval = txWaitingStage === "USER_APPROVAL";
  const isWaitingForTxConfirmation = txWaitingStage === "CONFIRMATION";
  const title = isWaitingForUserApproval
    ? "Please confirm the transaction"
    : // : "Waiting for the transaction to be mined";
      "Waiting for transaction confirmation";

  const message = isWaitingForUserApproval
    ? "Please approve the tx in your wallet."
    : "This might take a few moments";

  const explorerLink = useMemo(() => {
    if (txHash && explorerUrlBaseForTx) {
      return `${explorerUrlBaseForTx}/${txHash}`;
    } else {
      return null;
    }
  }, [explorerUrlBaseForTx, txHash]);

  console.log({ explorerUrlBaseForTx });
  console.log({ txHash });
  console.log({ explorerLink });

  return (
    <Modal open={open} className={classes.modal} onBackdropClick={closeModal}>
      <Fade in={open}>
        <div className={classes.txWaitingPanel}>
          {/* Quick-n-Dirty x button */}
          <div className={classes.upperRightCorner}>
            <IconButton onClick={closeModal}>
              <CloseIcon />
            </IconButton>
          </div>

          {/* Header */}
          <div className={classes.panelHeader}>
            <Typography variant={"h6"} className={classes.title}>
              {title}
            </Typography>
          </div>

          <div className={classes.messageContainer}>
            <Loader
              type={isWaitingForUserApproval ? "ThreeDots" : "Rings"}
              color={theme.palette.primary.main}
            />
            <Typography variant={"h6"}>{message}</Typography>
            {explorerLink && (
              <a
                href={explorerLink}
                target={"_blank"}
                rel={"noreferrer noopener"}
                className={classes.explorerLink}
              >
                <Typography variant={"h6"}>Open in {explorerName}</Typography>
              </a>
            )}
          </div>
        </div>
      </Fade>
    </Modal>
  );
});
