  // ****  Complex setters ****
  private failLoadingProcess(error: Error) {
    this.setErrorLoading(true);
    this.setDoneLoading(true);
  }

  private async batchReadAndUpdateCTokensStores() {
    const cTokenMarkets = this.allMarkets;
    // const [first, ...cTokenMarkets] = this.allMarkets;
    // const cTokenMarkets = [this.allMarkets[1], this.allMarkets[3]];
    // const cTokenMarkets = [this.allMarkets[1], this.allMarkets[3]];
    if (!cTokenMarkets || !cTokenMarkets.length) {
      return console.log(`No markets for cToken batch read and update`);
    } else if (!this.systemStore.compoundLensServiceInitialized) {
      return console.log("Lens service not yet initialized");
    }

    // console.log(`All cToken-markets ${this.allMarkets}`);
    const cTokenMetas = await this.systemStore.compoundLensService.cTokenMetadataAll(
      cTokenMarkets
    );
    // console.log(`Got metas : ${JSON.stringify(cTokenMetas.length)} `);
    // console.log(JSON.stringify(cTokenMetas, null, 2));
    // console.log(
    //   `cTokenBalances for ${this.cryptoWalletIntegrationStore.mainAddress}`
    // );
    const cTokenBalancesAll = await this.systemStore.compoundLensService.cTokenBalancesAll(
      cTokenMarkets,
      this.cryptoWalletIntegrationStore.mainAddress
    );

    // console.log(`Got balances : ${JSON.stringify(cTokenBalancesAll, null, 2)}`);
    const cTokenUnderlyingPricesAll = await this.systemStore.compoundLensService.cTokenUnderlyingPriceAll(
      cTokenMarkets
    );
    // console.log(`Got prices: ${JSON.stringify(cTokenUnderlyingPricesAll)} `);

    for (let i = 0; i < cTokenMetas.length; i++) {
      const cTokenMeta = cTokenMetas[i];
      const cTokenBalances = cTokenBalancesAll[i];
      const cTokenUnderlyingPrices = cTokenUnderlyingPricesAll[i];
      let matchingSTokenStore = this.cTokensStores.find(
        (cTokenStore) =>
          cTokenStore.underlyingAssetAddress ===
          cTokenMeta.underlyingAssetAddress
      );

      // DEV_NOTE : This is a temp solution to handle the fact that the 'Lens' returns 0x000 address for the native coin.
      if (
        !matchingSTokenStore &&
        cTokenMeta.underlyingAssetAddress === ZERO_ADDRESS
      ) {
        console.log("Looking for native coin cTokenStore");
        console.log(
          `POssible underlyings : ${this.cTokensStores.map(
            (cTokenStore) => cTokenStore.underlyingAssetAddress
          )}`
        );
        // TODO : CRITICAL : Lock on one standard
        matchingSTokenStore = this.cTokensStores.find((cTokenStore) =>
          [NATIVE_COIN_ASSET_ADDRESS, ZERO_ADDRESS].includes(
            cTokenStore.underlyingAssetAddress
          )
        );
      }

      if (matchingSTokenStore) {
        matchingSTokenStore.updateWithFreshState(
          cTokenMeta,
          cTokenBalances,
          cTokenUnderlyingPrices
        );

        const marketIsColleterable = this.marketsUserIsIn.includes(
          matchingSTokenStore.cTokenAddress
        );
        matchingSTokenStore.updateIsInCollaterable(marketIsColleterable);
      } else {
        console.log(
          `No cTokenStore found for meta of ${cTokenMeta.underlyingAssetAddress}`
        );
      }
    }
  }

  // @action
  // private setTokens(tokens: TokenStore[] | null): void {
