import {
  useAccountStore,
  useCryptoWalletIntegrationStore,
  useLendingNetworksStore,
  useModalsStore,
  useSystemStore,
} from "../../../store/storeHooks";
import { TAssetInMarketParameters } from "../../../services/lendingNetworksService/ILendingNetworksService";
import { useCallback } from "react";
import { ethers } from "ethers";
import { useTxWaitingLogicExplicit } from "../txWaitingModal/txWaitingHooks";
import { SingleLNStore } from "../../../store/multiInstanceStores/SingleLNStore";
import { LENDING_NETWORK_NAMES } from "../../../services/lendingNetworksService/lendingNetworksParams";

export function useIncentiveModalState() {
  const accountStore = useAccountStore();
  const modalsStore = useModalsStore();
  const systemStore = useSystemStore();
  const cryptoWalletConnectionStore = useCryptoWalletIntegrationStore();
  const lendingNetworksStore = useLendingNetworksStore();
  const activeLNStore = lendingNetworksStore.activeLNStore;

  const incentiveTokenSymbol = activeLNStore?.distributionAssetParams.symbol;
  const incentiveTokenName = activeLNStore?.distributionAssetParams.name;
  const incentiveTokenAddress = activeLNStore?.distributionAssetParams.address;

  const compBalanceUnits = activeLNStore?.compBalanceUnits || 0;
  const compAllocatedUnits = activeLNStore?.compAllocatedUnits || 0;

  const showNativeCoinBalance =
    activeLNStore?.name === LENDING_NETWORK_NAMES.fuseAlpha;
  const nativeCoinSymbol = systemStore.nativeCoinSymbol;
  const nativeCoinName = systemStore.nativeCoinName;
  const nativeCoinBalance = cryptoWalletConnectionStore.nativeCoinBalance;

  return {
    activeLNStore,
    incentiveTokenSymbol,
    incentiveTokenName,
    incentiveTokenAddress,
    compBalanceUnits,
    compAllocatedUnits,
    showNativeCoinBalance,
    nativeCoinSymbol,
    nativeCoinName,
    nativeCoinBalance,
  };
}

export function useOnClaimLnIncentive(
  activeLNStore: SingleLNStore | undefined,
  closeModal: () => void,
  setActiveTxHash: (val: string) => void,
  setWaitingForUserApproval: (val: boolean) => void,
  setWaitingForTxConfirmation: (val: boolean) => void
) {
  const createClaimLnIncentiveTx = useCallback(() => {
    return activeLNStore!.claimLnIncentiveToken();
  }, [activeLNStore]);

  const wrappedClaimTx = useTxWaitingLogicExplicit(
    createClaimLnIncentiveTx,
    closeModal,
    setActiveTxHash,
    setWaitingForUserApproval,
    setWaitingForTxConfirmation,
    true
  );

  const onClaimClicked = useCallback(async () => {
    if (activeLNStore) {
      console.log(`Claim LN incentive button clicked`);
      await wrappedClaimTx();
      closeModal();
      console.log(`Claim LN incentive is done`);
    } else {
      return console.log("No Ln Store for claiming");
    }
  }, [activeLNStore, closeModal, wrappedClaimTx]);

  return onClaimClicked;
}
