import { BigNumber, Contract } from "ethers";
import { Signer } from "@ethersproject/abstract-signer";
import { Provider } from "@ethersproject/abstract-provider";
import CompoundLensJson from "../../../solidityInterfaces/CompoundLens.json";
import { CompoundLens } from "../../../solidityInterfaces/CompoundLens";
import {
  ICompoundLensService,
  TAccountLimits,
  TCompBalanceMetadataExt,
  TCTokenBalances,
  TCTokenMetadata,
  TCTokenUnderlyingPrice,
} from "./ICompoundLensService";
import { parseEther } from "ethers/lib/utils";
import { etheresStructResponseToObject } from "../contractsIntegrationUtils";

export class CompoundLensService implements ICompoundLensService {
  private compoundLensContract: CompoundLens;

  constructor(
    compoundLensAddress: string,
    private signerOrProvider: Signer | Provider
  ) {
    this.compoundLensContract = new Contract(
      compoundLensAddress,
      CompoundLensJson.abi,
      signerOrProvider
    ) as CompoundLens;
  }

  public updateCompoundLensAddress(newCompoundLensAddress: string) {
    console.log(`Updating compoundLens address to ${newCompoundLensAddress}`);
    if (newCompoundLensAddress === "") {
      console.log("Got an empty compound lens address");
    }

    this.compoundLensContract = new Contract(
      newCompoundLensAddress,
      CompoundLensJson.abi,
      this.signerOrProvider
    ) as CompoundLens;
  }

  async cTokenMetadata(cTokenAddress: string): Promise<TCTokenMetadata> {
    if (!this.isInitialized()) {
      console.log("NotInitialized for : cTokenMetadataAll");
    }
    return etheresStructResponseToObject(
      await this.compoundLensContract.callStatic.cTokenMetadata(cTokenAddress)
    );
  }

  async cTokenMetadataAll(
    cTokenAddresses: string[]
  ): Promise<TCTokenMetadata[]> {
    // this.safeAddress();
    if (!this.isInitialized()) {
      console.log("NotInitialized for : cTokenMetadataAll");
      return [];
    }
    const rawMetas = await this.compoundLensContract.callStatic.cTokenMetadataAll(
      cTokenAddresses
    );

    return rawMetas.map((rawMeta) => etheresStructResponseToObject(rawMeta));
  }

  async cTokenBalancesAll(
    cTokenAddresses: string[],
    account: string
  ): Promise<TCTokenBalances[]> {
    // this.safeAddress();
    if (!this.isInitialized()) {
      console.log("NotInitialized for : cTokenBalancesAll");
      return [];
    }
    const rawMetas = await this.compoundLensContract.callStatic.cTokenBalancesAll(
      cTokenAddresses,
      account
    );

    return rawMetas.map((rawMeta) => etheresStructResponseToObject(rawMeta));
  }

  async cTokenUnderlyingPriceAll(
    cTokenAddresses: string[]
  ): Promise<TCTokenUnderlyingPrice[]> {
    // this.safeAddress();
    if (!this.isInitialized()) {
      console.log("NotInitialized for : cTokenUnderlyingPriceAll");
      return [];
    }
    const rawMetas = await this.compoundLensContract.callStatic.cTokenUnderlyingPriceAll(
      cTokenAddresses
    );

    return rawMetas.map((rawMeta) => etheresStructResponseToObject(rawMeta));
  }

  async getAccountLimits(
    comptrollerAddress: string,
    account: string
  ): Promise<TAccountLimits> {
    // this.safeAddress();
    if (!this.isInitialized()) {
      console.log("NotInitialized for : getAccountLimits");
      return {
        shortfall: BigNumber.from(0),
        liquidity: BigNumber.from(0),
        marketsIn: [],
      };
    }
    const rawAccountLimits = await this.compoundLensContract.getAccountLimits(
      comptrollerAddress,
      account
    );
    const [marketsIn, liquidity, shortfall] = rawAccountLimits;

    return {
      marketsIn,
      liquidity,
      shortfall,
    };
  }

  async fetchCompBalanceMetadataExt(
    compToken: string,
    comptrollerAddress: string,
    account: string
  ): Promise<TCompBalanceMetadataExt> {
    // this.safeAddress();
    if (!this.isInitialized()) {
      console.log("NotInitialized for : fetchCompBalanceMetadataExt");
      return {
        balance: BigNumber.from(0),
        allocated: BigNumber.from(0),
        votes: BigNumber.from(0),
      };
    }

    console.log(`Fetching comp-balance-metadata-ext for inventive`);
    console.log({ compToken });
    console.log({ comptrollerAddress });
    console.log({ account });

    const rawCompBalanceMetaExt = await this.compoundLensContract.callStatic.getCompBalanceMetadataExt(
      compToken,
      comptrollerAddress,
      account
    );

    return rawCompBalanceMetaExt;
  }

  private isInitialized(): boolean {
    console.log(
      `Compound lens contract : ${this.compoundLensContract.address}`
    );
    return this.compoundLensContract.address !== "";
  }

  private safeAddress() {
    if (!this.isInitialized) {
      throw new Error(`Compound lens address not initialized`);
    }
  }
}
