import React, { ReactNode, useCallback, useMemo, useState } from "react";
import { observer } from "mobx-react";
import {
  useCalculateChangeInBorrowLimits,
  useMarketInteractionModalBase,
} from "../MarketInteractionModal/marketInteractionModalHooks";
import {
  useTxWaitingLogic,
  useTxWaitingStateExplicit,
} from "../txWaitingModal/txWaitingHooks";
import { IncentiveModal } from "./IncentiveModal";
import {
  useIncentiveModalState,
  useOnClaimLnIncentive,
} from "./incentiveModalHooks";
import {
  TTxWaitingStage,
  TxWaitingModal,
} from "../txWaitingModal/TxWaitingModal";
import { useSystemStore } from "../../../store/storeHooks";

interface IProps {
  open: boolean;
  closeModal: () => void;
}

export const ConnectedIncentiveModal = observer<
  React.FunctionComponent<IProps>
>((props) => {
  const { open, closeModal } = props;

  const systemStore = useSystemStore();

  const {
    activeLNStore,
    incentiveTokenAddress,
    incentiveTokenName,
    incentiveTokenSymbol,
    compAllocatedUnits,
    compBalanceUnits,
    nativeCoinName,
    nativeCoinSymbol,
    showNativeCoinBalance,
    nativeCoinBalance,
  } = useIncentiveModalState();

  const {
    activeTxHash,
    setActiveTxHash,
    waitingForUserApproval,
    waitingForTxConfirmation,
    setWaitingForUserApproval,
    setWaitingForTxConfirmation,
  } = useTxWaitingStateExplicit();

  const onClaimClicked = useOnClaimLnIncentive(
    activeLNStore,
    closeModal,
    setActiveTxHash,
    setWaitingForUserApproval,
    setWaitingForTxConfirmation
  );

  let whyCannotClaim = "";
  let canClaim = true;

  if (compAllocatedUnits === 0) {
    canClaim = false;
    whyCannotClaim = "Nothing to claim";
  } else if (waitingForUserApproval) {
    canClaim = false;
    whyCannotClaim = "Please approve tx";
  }
  if (waitingForTxConfirmation) {
    canClaim = false;
    whyCannotClaim = "Waiting for TX confirmation";
  }

  if (waitingForTxConfirmation || waitingForUserApproval) {
    const txWaitingStage: TTxWaitingStage = waitingForTxConfirmation
      ? "CONFIRMATION"
      : "USER_APPROVAL";

    // TODO : Use the directo logic hook
    return (
      <TxWaitingModal
        open={open}
        closeModal={closeModal}
        txWaitingStage={txWaitingStage}
        txHash={activeTxHash}
        explorerUrlBaseForTx={systemStore.explorerBaseTxUrl}
        explorerName={systemStore.explorerName}
      />
    );
  }

  return (
    <IncentiveModal
      open={open}
      closeModal={closeModal}
      incentiveTokenAddress={incentiveTokenAddress || ""}
      incentiveTokenName={incentiveTokenName || ""}
      incentiveTokenSymbol={incentiveTokenSymbol || ""}
      balanceInUnits={compBalanceUnits}
      unclaimedInUnits={compAllocatedUnits}
      canClaim={canClaim}
      onClaim={onClaimClicked}
      whyCannotClaim={whyCannotClaim}
      showNativeCoinBalance={showNativeCoinBalance}
      nativeCoinName={nativeCoinName}
      nativeCoinBalanceInUnits={nativeCoinBalance}
    />
  );
});
