import {
  useAccountStore,
  useLendingNetworksStore,
  useModalsStore,
  useSystemStore,
} from "../../../store/storeHooks";
import { TAssetInMarketParameters } from "../../../services/lendingNetworksService/ILendingNetworksService";
import { SingleCTokenStore } from "../../../store/multiInstanceStores/SingleCTokenStore";
import { useCallback, useMemo, useState } from "react";
import { TZeroOrOne } from "./base/MarketInteractionModal";
import { ethers } from "ethers";
import { useTxWaitingLogicExplicit } from "../txWaitingModal/txWaitingHooks";

export function useMarketInteractionModalBase() {
  const accountStore = useAccountStore();
  const modalsStore = useModalsStore();
  const lendingNetworksStore = useLendingNetworksStore();
  const systemStore = useSystemStore();
  const activeLNStore = lendingNetworksStore.activeLNStore;
  const assetAddressToParamsMap =
    activeLNStore?.assetBaseAddressToParams ||
    new Map<string, TAssetInMarketParameters>();
  const assetBaseAddress = modalsStore.assetBaseAddress;

  const assetParams = assetAddressToParamsMap.get(assetBaseAddress);
  const isNativeCoin = assetParams?.symbol === systemStore.nativeCoinSymbol;

  const cTokenStore = activeLNStore?.baseAssetsToCTokenStore.get(
    assetBaseAddress
  );

  const cToken = cTokenStore?.cTokenAddress || "";

  const supplyTokenAddress = assetParams?.address || "";
  const supplyTokenSymbol = assetParams?.symbol || "";
  const supplyTokenName = assetParams?.name || "";
  const distributionTokenSymbol =
    activeLNStore?.distributionAssetParams.symbol || "NONE";

  const walletBalance = cTokenStore?.accountUnderlyingBalanceInUnits || 0;
  const hasWalletBalance = walletBalance > 0;
  const needToApprove = !isNativeCoin; // False for native

  const pricePerUnitUsd = cTokenStore?.underlyingUsdPrice || 0;
  const collateralFactorInUnits = cTokenStore?.collateralFactorInUnits || 0;
  const maximillionAddress = activeLNStore?.maximillionAddress || "";
  const currentLiquidityCashUsd = activeLNStore?.liquidityUsd || 0;
  // const currentBorrowLimitUsd = activeLNStore?.collateralPotentialInUSD || 0;

  const usedBorrowPercentage = activeLNStore?.usedBorrowPercentage || 0;
  const totalAccountBorrowBalanceUsd =
    activeLNStore?.totalAccountBorrowBalanceUsd || 0;
  const collateralPotentialInUSD = activeLNStore?.collateralPotentialInUSD || 0;
  const priceCalculationDecimals = activeLNStore?.priceCalculationDecimals || 6;

  const distributionAssetApy = activeLNStore?.distributionAssetApy || 0;
  const supplyApy = cTokenStore?.supplyApyInPercentages || 0;
  const borrowApy = cTokenStore?.borrowApyInPercentages || 0;

  // Can they borrow more ?
  const currentLiquidityUsd = activeLNStore?.liquidityUsd || 0;
  const hasAvailableLiquidity = currentLiquidityUsd > 0; // True means user can borrow&withdraw

  // How many units can the user borrow more
  const liquidityInAssetUnits = currentLiquidityUsd / pricePerUnitUsd;

  const isCollaterable = cTokenStore?.isCollaterable || false;

  return {
    accountStore,
    modalsStore,
    lendingNetworksStore,
    activeLNStore,
    assetBaseAddress,
    maximillionAddress,
    isNativeCoin,
    cTokenStore,
    cToken,
    supplyTokenAddress,
    supplyTokenSymbol,
    supplyTokenName,
    distributionTokenSymbol,
    isCollaterable,
    collateralPotentialInUSD,

    walletBalance,
    hasWalletBalance,
    needToApprove,

    priceCalculationDecimals,
    pricePerUnitUsd,
    collateralFactorInUnits,
    currentLiquidityCashUsd,
    // currentBorrowLimitUsd,
    usedBorrowPercentage,
    totalAccountBorrowBalanceUsd,

    distributionAssetApy,
    supplyApy,
    borrowApy,

    hasAvailableLiquidity,
    liquidityInAssetUnits,
  };
}

export function useMarketInteractionModalTabsState() {
  const [activeTabIndex, setActiveTabIndex] = useState<TZeroOrOne>(0);

  const borrowTabIndex = 0;
  const repayTabIndex = 1;
  const tab0Title = "Borrow";
  const tab1Title = "Repay";

  const isBorrow = activeTabIndex === borrowTabIndex;
  const isRepay = activeTabIndex === repayTabIndex;

  return {
    activeTabIndex,
    setActiveTabIndex,
  };
}

export function useMarketInteractionActiveTabState(activeTabIndex: TZeroOrOne) {
  return [activeTabIndex === 0, activeTabIndex === 1];
}

export function useOnEnableClicked(
  cToken: string,
  assetBaseAddress: string,
  closeModal: () => void,
  setActiveTxHash: (val: string) => void,
  setWaitingForUserApproval: (val: boolean) => void,
  setWaitingForTxConfirmation: (val: boolean) => void
) {
  const accountStore = useAccountStore();

  const createEnableTx = useCallback(() => {
    return accountStore.approveErc20Usage(
      assetBaseAddress,
      cToken,
      ethers.constants.MaxUint256
    );
  }, [accountStore, assetBaseAddress, cToken]);

  const wrappedEnableTx = useTxWaitingLogicExplicit(
    createEnableTx,
    closeModal,
    setActiveTxHash,
    setWaitingForUserApproval,
    setWaitingForTxConfirmation,
    false
  );

  const onEnableClicked = useCallback(async () => {
    if (!cToken) {
      return console.log("No CToken Address");
    }

    await wrappedEnableTx();

    console.log(`Enable button clicked`);
    await console.log(`Enable is done !`);
  }, [cToken, wrappedEnableTx]);

  return onEnableClicked;
}

/**
 *
 * @param currentBorrowLimitUsd
 * @param totalAccountBorrowBalanceUsd sum of all active borrows in USD
 * @param actionInUnits DEV_NOTE : Units should be negative for actions that reduce collateral (borrow/withdraw)
 * @param unitPriceInUsd
 * @param collateralFactorInUnits
 */
export function useCalculateChangeInBorrowLimits(
  currentBorrowLimitUsd: number,
  totalAccountBorrowBalanceUsd: number,
  actionInUnits: number,
  unitPriceInUsd: number,
  collateralFactorInUnits: number,
  // Indicates the decimals used to calculate the prices in the smart contract.
  // This will prevent js float calculation from interfering.
  // For example, when withdrawing maximum amount, the float rounding
  // could cause the app to think that this will lead to shortfall
  // (because the value will we an something like -1e14.
  pricePrecision: number
) {
  const priceSensitivizer = buildSensitivizer(pricePrecision);
  const actionEffectInUsd = priceSensitivizer(actionInUnits * unitPriceInUsd);

  const collateralValueOfActionEffect = priceSensitivizer(
    actionEffectInUsd * collateralFactorInUnits
  );

  const borrowLimitAfterChange = priceSensitivizer(
    currentBorrowLimitUsd + collateralValueOfActionEffect
  );

  const usedBorrowLimitPercentageAfterChange =
    priceSensitivizer(totalAccountBorrowBalanceUsd / borrowLimitAfterChange) *
    100;

  const actionLeadsToShortfall = usedBorrowLimitPercentageAfterChange > 100;

  return {
    actionEffectInUsd,
    actionLeadsToShortfall,
    collateralValueOfActionEffect,
    borrowLimitAfterChange,
    usedBorrowLimitPercentageAfterChange,
  };
}

/**
 *
 * @param currentBorrowLimitUsd
 * @param totalAccountBorrowBalanceUsd sum of all active borrows in USD
 * @param actionInUnits DEV_NOTE : Units should be negative for actions that reduce collateral (borrow/withdraw)
 * @param unitPriceInUsd
 * @param collateralFactorInUnits
 */
export function useCalculateChangeInBorrowLimitsForBorrow(
  collateralPotentialInUSD: number,
  currentBorrowLimitUsd: number,
  totalAccountBorrowBalanceUsd: number,
  actionInUnits: number,
  unitPriceInUsd: number,
  // Indicates the decimals used to calculate the prices in the smart contract.
  // This will prevent js float calculation from interfering.
  // For example, when withdrawing maximum amount, the float rounding
  // could cause the app to think that this will lead to shortfall
  // (because the value will we an something like -1e14.
  pricePrecision: number
) {
  const priceSensitivizer = buildSensitivizer(pricePrecision);
  const actionEffectInUsd = priceSensitivizer(actionInUnits * unitPriceInUsd);

  const collateralValueOfActionEffect = actionEffectInUsd;

  const borrowLimitAfterChange = priceSensitivizer(
    currentBorrowLimitUsd + collateralValueOfActionEffect
    // currentBorrowLimitUsd
  );

  const totalAccountBorrowBalanceUsdAfterAction =
    totalAccountBorrowBalanceUsd + Math.abs(actionEffectInUsd);

  const usedBorrowLimitPercentageAfterChange =
    priceSensitivizer(
      totalAccountBorrowBalanceUsdAfterAction / collateralPotentialInUSD
    ) * 100;

  const actionLeadsToShortfall = usedBorrowLimitPercentageAfterChange > 100;

  return {
    actionEffectInUsd,
    actionLeadsToShortfall,
    collateralValueOfActionEffect,
    borrowLimitAfterChange,
    usedBorrowLimitPercentageAfterChange,
  };
}

