export function usePriceInSensitivity(price: number, sensitivity: number) {
  return useMemo(() => {
    return sensitivizePrice(price, sensitivity);
  }, [price, sensitivity]);
}

export function useSensitivizer(sensitivity: number) {
  return useCallback(
    (price: number) => {
      return sensitivizePrice(price, sensitivity);
    },
    [sensitivity]
  );
}

export function buildSensitivizer(sensitivity: number) {
  return (price: number) => {
    return sensitivizePrice(price, sensitivity);
  };
}

function sensitivizePrice(price: number, sensitivity: number): number {
  return parseFloat(price.toFixed(sensitivity));
}
