import {
  ILendingNetworkGist,
  ILendingNetworkLiveState,
  ILendingNetworkParameters,
  ILendingNetworksService,
} from "./ILendingNetworksService";
import { IS_DEV } from "../../configs";
import { fetchLocalJson } from "../networkActions";
import LocalNetworkDevLNAddresses from "../../solidityInterfaces/localLNaddresses.json";
// TODO : CRITICAL almost : Add 'decimals on chain' to make sure that decimals are right for each chain.
import LocalNetworkDevTokensAddresses from "../../solidityInterfaces/localTokensAddresses.json";
import { NATIVE_COIN_ASSET_ADDRESS } from "../../constants/constantAddresses";
import { LendingNetworksParam } from "./lendingNetworksParams";

export class LendingNetworksService implements ILendingNetworksService {
  async fetchALlLendingNetworksParameters(): Promise<
    ILendingNetworkParameters[]
  > {
    const lnParams: ILendingNetworkParameters[] = [];

    if (IS_DEV) {
      const devNetworksgist = await this.fetchDevNetworkGist();

      lnParams.push(...devNetworksgist);
    } else {
      lnParams.push(...LendingNetworksParam);
    }

    return lnParams;
  }

  async fetchAllLendingNetworksGists(): Promise<ILendingNetworkGist[]> {
    return [];
    const allLNsGists: ILendingNetworkGist[] = [];
  }

  async fetchLendingNetworkLiveState(
    networkId: string
  ): Promise<ILendingNetworkLiveState> {
    return {
      participatingSuppliers: 0,
      tvl: 0,
    };
  }

  private async fetchDevNetworkGist() {
    const devNetworksgist = await fetchLocalJson<ILendingNetworkGist[]>(
      "lnParameters/devLnParams.json"
    );

    // DEV_NOTE : We have one, and we want to at least semi-dynamically get the addresses
    devNetworksgist[0].id = LocalNetworkDevLNAddresses.unitroller;
    devNetworksgist[0].unitrollerAddress =
      LocalNetworkDevLNAddresses.unitroller;
    devNetworksgist[0].maximillionAddress =
      LocalNetworkDevLNAddresses.maxmimilion;
    devNetworksgist[0].distributionAssetParameters.address =
      LocalNetworkDevTokensAddresses["WFUSE"];

    for (let assetInMarket of devNetworksgist[0].assets) {
      const { symbol } = assetInMarket;
      // @ts-ignore
      const assetAddress = LocalNetworkDevTokensAddresses[symbol];
      const assetAddressToUse =
        symbol === "FUSE" ? NATIVE_COIN_ASSET_ADDRESS : assetAddress;

      if (!assetAddressToUse) {
        // This quick-fail will save some time debugging
        throw new Error(`No addres for asset ${symbol}`);
      } else {
        assetInMarket.address = assetAddressToUse;
      }
    }

    return devNetworksgist;
  }
}
