import { Signer } from "@ethersproject/abstract-signer";
import { Provider } from "@ethersproject/abstract-provider";
import { ICTokensBuilderService } from "./ICTokensBuilderService";
import { ICErc20Service } from "./ICErc20Service";
import { CErc20Service } from "./CErc20Service";
import { CEtherService } from "./CEtherService";

export class CTokensBuilderService implements ICTokensBuilderService {
  constructor(private signerOrProvider: Signer | Provider) {}

  buildCErc20Service(cErc20Address: string): ICErc20Service {
    return new CErc20Service(cErc20Address, this.signerOrProvider);
  }

  buildCEtherService(cEther: string, maximillion: string): CEtherService {
    return new CEtherService(cEther, maximillion, this.signerOrProvider);
  }
}
