// TODO : SEMI-CRITICAL : Have the right value for each chain.
const DAYS_IN_YEAR = 365;
const MAINNET_ETHERUM_BLOCKS_PER_YEAR = 2102400;

const ROPSTEN_BLOCKS_PER_MINUTE = 5;
const ROPSTEN_BLOCK_PER_DAY = ROPSTEN_BLOCKS_PER_MINUTE * 60 * 24;
const ROPSTEN_BLOCK_PER_YEAR = ROPSTEN_BLOCK_PER_DAY * DAYS_IN_YEAR;

const FUSE_BLOCKS_PER_MINUTE = 5;
const FUSE_BLOCK_PER_DAY = FUSE_BLOCKS_PER_MINUTE * 60 * 24;
const FUSE_BLOCK_PER_YEAR = FUSE_BLOCK_PER_DAY * DAYS_IN_YEAR;

