import { BigNumber, Contract, ContractTransaction } from "ethers";
import { Signer } from "@ethersproject/abstract-signer";
import { Provider } from "@ethersproject/abstract-provider";
import CEtherJson from "../../../solidityInterfaces/CEther.json";
import MaximillionJson from "../../../solidityInterfaces/Maximillion.json";
import { ICEtherService } from "./ICEtherService";
import { CEther } from "../../../solidityInterfaces/CEther";
import { Maximillion } from "../../../solidityInterfaces/Maximillion";
import { formatEther, parseEther } from "ethers/lib/utils";

export class CEtherService implements ICEtherService {
  private cEther: CEther;
  private maximillion: Maximillion;

  constructor(
    cEtherAddress: string,
    maximillionAddress: string,
    private signerOrProvider: Signer | Provider
  ) {
    this.cEther = new Contract(
      cEtherAddress,
      CEtherJson.abi,
      signerOrProvider
    ) as CEther;

    this.maximillion = new Contract(
      maximillionAddress,
      MaximillionJson.abi,
      signerOrProvider
    ) as Maximillion;
  }

  public async mint(amount: BigNumber): Promise<ContractTransaction> {
    return this.cEther.mint({ value: amount });
  }

  public async withdraw(amount: BigNumber): Promise<ContractTransaction> {
    return this.cEther.redeemUnderlying(amount);
  }

  public async withdrawByOTokens(
    oTokensAmount: BigNumber
  ): Promise<ContractTransaction> {
    return this.cEther.redeem(oTokensAmount);
  }

  public async borrow(amount: BigNumber): Promise<ContractTransaction> {
    return this.cEther.borrow(amount);
  }

  public async repayBorrow(amount: BigNumber): Promise<ContractTransaction> {
    throw new Error(`Cannot call 'repayBorrow' for oNative`);
  }

  public async repayBorrowWithMaximillion(
    borrowerAddress: string,
    amount: BigNumber
  ): Promise<ContractTransaction> {
    console.log(`CEther repay borrow : ${amount.toString()}`);
    console.log(`CEther repay borrow : ${formatEther(amount)}`);
    return this.maximillion.repayBehalf(borrowerAddress, { value: amount });
  }
}
