import { BigNumber } from "@ethersproject/bignumber";

export type TAssetInMarketParameters = {
  name: string;
  symbol: string;
  address: string;
  decimals: number;
  isNativeCoin: boolean;
};

export const EMPTY_ASSET_IN_MARKET_PARAMETERS: TAssetInMarketParameters = {
  address: "",
  symbol: "",
  name: "",
  decimals: 0,
  isNativeCoin: false,
};

// "Parameters" refers to the unChangeable elements (name, id, tokens, etc..)
// TODO : Think of a better name
export interface ILendingNetworkParameters {
  id: string;
  chainId: string;
  name: string;
  lnHomePage: string;
  unitrollerAddress: string;
  maximillionAddress: string;
  assets: TAssetInMarketParameters[];
  distributionAssetParameters: TAssetInMarketParameters;
}

export interface ILendingNetworkLiveState {
  tvl: number;
  participatingSuppliers: number;
}

export interface ILendingNetworkGist
  extends ILendingNetworkParameters,
    ILendingNetworkLiveState {}

export interface ITokenData {
  cToken: string;
  cTokenName: string;
  cTokenSymbol: string;
  cTokenDecimals: string;
  exchangeRateCurrent: string;
  supplyRatePerBlock: string;
  borrowRatePerBlock: string;
  reserveFactorMantissa: string;
  totalBorrows: string;
  totalReserves: string;
  totalSupply: string;
  totalCash: string;
  isListed: boolean;
  collateralFactorMantissa: string;
}

export interface IUnderlyingMetadata {
  underlyingAssetAddress: string;
  underlyingDecimals: string;
  underlyingName: string;
  underlyingSymbol: string;
}

export interface ITokenUnderlyingPrice {
  cToken: string;
  underlyingPrice: string;
}

export interface ILendingNetworksService {
  fetchALlLendingNetworksParameters(): Promise<ILendingNetworkParameters[]>;
  fetchAllLendingNetworksGists(): Promise<ILendingNetworkGist[]>;

  fetchLendingNetworkLiveState(
    networkId: string
  ): Promise<ILendingNetworkLiveState>;
}
