import React from "react";
// @ts-ignore
import AnimatedNumber from "animated-number-react";

interface IProps {
  value: number;
}

function standardSumFormat(n: number) {
  return (+n.toFixed(2)).toLocaleString();
}

export const AnimatedNumberText = React.memo<IProps>((props) => {
  const { value } = props;

  return <AnimatedNumber value={value} formatValue={standardSumFormat} />;
});
