import { action, computed, makeObservable, observable } from "mobx";

type TModalIds =
  | "SUPPLY_MODAL"
  | "BORROW_MODAL"
  | "COLLATERAL_MODAL"
  | "INCENTIVE_MODAL";
type TCurrentModalIds = TModalIds | "NONE";

export class ModalsStore {
  @observable SUPPLY_MODAL_ID = "SUPPLY_MODAL";

  @observable currentModalId: TCurrentModalIds = "NONE";
  @observable assetBaseAddress: string = "";

  @computed get isSupplyModalActive(): boolean {
    return this.currentModalId === "SUPPLY_MODAL";
  }

  @computed get isBorrowModalActive(): boolean {
    return this.currentModalId === "BORROW_MODAL";
  }

  @computed get isCollateralModalActive(): boolean {
    return this.currentModalId === "COLLATERAL_MODAL";
  }

  @computed get isInventiveModalActive(): boolean {
    return this.currentModalId === "INCENTIVE_MODAL";
  }

  constructor() {
    makeObservable(this);
  }

  // **** Modal management ****

  public showSupplyModal(supplyAssetBaseAddress: string) {
    this.showAssetActionModal("SUPPLY_MODAL", supplyAssetBaseAddress);
  }

  public showBorrowModal(borrowAssetBaseAddress: string) {
    this.showAssetActionModal("BORROW_MODAL", borrowAssetBaseAddress);
  }

  public showCollateralModal(borrowAssetBaseAddress: string) {
    this.showAssetActionModal("COLLATERAL_MODAL", borrowAssetBaseAddress);
  }

  public showIncentiveModal() {
    this.setCurrentModalId("INCENTIVE_MODAL");
  }

  // **** Complex setters ****
  private showAssetActionModal(
    supplyModalId: TModalIds,
    assetBaseAddress: string
  ) {
    // DEV_NOTE : Setting the asset before showing the modal to ensure that asset data will be found (no showing "mid-way" UI state)
    this.setAssetBaseAddress(assetBaseAddress);
    this.setCurrentModalId(supplyModalId);
  }

  /**
   * Will only have any effect if the current modal is 'supply'
   */
  public hideSupplyModal() {
    if (this.isSupplyModalActive) {
      this.setCurrentModalId("NONE");
    }
  }

  /**
   * Will only have any effect if the current modal is 'borrow'
   */
  public hideBorrowModal() {
    if (this.isBorrowModalActive) {
      this.setCurrentModalId("NONE");
    }
  }

  /**
   * Will only have any effect if the current modal is 'collateral'
   */
  public hideCollateralModal() {
    if (this.isCollateralModalActive) {
      this.setCurrentModalId("NONE");
    }
  }

  /**
   * Will only have any effect if the current modal is 'incentive'
   */
  public hideIncentiveModal() {
    if (this.isInventiveModalActive) {
      this.setCurrentModalId("NONE");
    }
  }

  // ****  Observables setter actions ****
  @action("setCurrentModalId")
  private setCurrentModalId(currentModalId: TCurrentModalIds) {
    console.log(`New current modal id ${currentModalId}`);
    this.currentModalId = currentModalId;
  }

  @action("setAssetBaseAddress")
  private setAssetBaseAddress(assetBaseAddress: string) {
    this.assetBaseAddress = assetBaseAddress;
  }
}
