import { BigNumber, Contract, ContractTransaction } from "ethers";
import { Signer } from "@ethersproject/abstract-signer";
import { Provider } from "@ethersproject/abstract-provider";
import ComptrollerJson from "../../../solidityInterfaces/Comptroller.json";
import { IComptrollerService } from "./IComptrollerService";
import { Comptroller } from "../../../solidityInterfaces/Comptroller";
import { toJS } from "mobx";

export class ComptrollerService implements IComptrollerService {
  // We will use the unitroller address but with the 'comptroller' interface (As it is only a proxy)
  private unitrollerProxyContract: Comptroller;

  constructor(unitrollerAddress: string, signerOrProvider: Signer | Provider) {
    console.log("Before init uni");

    this.unitrollerProxyContract = new Contract(
      unitrollerAddress,
      ComptrollerJson.abi,
      signerOrProvider
    ) as Comptroller;

    console.log("Initialized unitroller");
  }

  public getComptrollerProxyAddress(): string {
    return this.unitrollerProxyContract.address;
  }

  // TODO : This is not called for now, check if it is needed and then check if it works
  public updateUnitrollerAddress(unitrollerAddress: string): void {
    const after = this.unitrollerProxyContract.attach(unitrollerAddress);
    console.log(`Param is ${unitrollerAddress}`);
    console.log(`Result address is ${toJS(after.address)}`);
    console.log(
      `Property address is ${toJS(this.unitrollerProxyContract.address)}`
    );
  }

  /**
   * @ DEV_NOTE : All of the CTokens of this comptroller
   */
  async getAllMarkets(): Promise<string[]> {
    const markets = await this.unitrollerProxyContract.getAllMarkets();
    return markets;
  }

  async enterMarkets(cTokens: string[]): Promise<ContractTransaction> {
    return this.unitrollerProxyContract.enterMarkets(cTokens);
    // const res = await this.unitrollerProxyContract.callStatic.enterMarkets(
    //   cTokens
    // );
    //
    // console.log(`enter markets : ${res}`);
    //
    // return (null as any) as ContractTransaction;
  }
  async exitMarket(cToken: string): Promise<ContractTransaction> {
    return this.unitrollerProxyContract.exitMarket(cToken);
  }

  async claimIncentiveFromAllMarkets(
    account: string
  ): Promise<ContractTransaction> {
    return this.unitrollerProxyContract["claimComp(address)"](account);
  }
}
