import React, { useMemo } from "react";
import { makeStyles } from "@material-ui/core";
import { BaseRates, TRatesRow } from "./BaseRates";

interface IProps {
  actionName: string;
  assetSymbol: string;
  assetSupplyApy: number;
  distributionAssetSymbol: string;
  distributionAssetApy: number;
}

export const ActionRates = React.memo<IProps>((props) => {
  const {
    actionName,
    assetSymbol,
    assetSupplyApy,
    distributionAssetSymbol,
    distributionAssetApy,
  } = props;

  const rateRows = useMemo<TRatesRow[]>(() => {
    const firstRow: TRatesRow = {
      label: `${actionName} APY`,
      tokenSymbol: assetSymbol,
      value: assetSupplyApy,
      valueSuffix: "%",
    };
    const secondRow: TRatesRow = {
      label: "Distribution APY",
      tokenSymbol: distributionAssetSymbol,
      value: distributionAssetApy,
      valueSuffix: "%",
    };

    return [firstRow, secondRow];
  }, [
    actionName,
    assetSupplyApy,
    assetSymbol,
    distributionAssetApy,
    distributionAssetSymbol,
  ]);

  return <BaseRates title={`${actionName} Rates`} rateRows={rateRows} />;
});
