import React from "react";
import {
  Button,
  Fade,
  IconButton,
  makeStyles,
  Modal,
  Typography,
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";
import { TokenIcon } from "../../icons/TokenIcon";
import { BRAND_MAIN_COLOR, GRAYISH_TEXT } from "../../../theme/themeConstants";
import { BorrowLimitRates } from "../MarketInteractionModal/base/rates/BorrowLimitRates";
import { ModalActionButton } from "../MarketInteractionModal/base/ModalActionButton";
import {
  TTxWaitingStage,
  TxWaitingModal,
} from "../txWaitingModal/TxWaitingModal";

interface IProps {
  open: boolean;
  closeModal: () => void;

  actionAssetName: string;
  actionAssetSymbol: string;

  currentBorrowLimit: number;
  usedBorrowLimitPercentage: number;
  usedBorrowLimitPercentageAfterChange: number;
  borrowLimitAfterChange: number;
  showPredictedBorrowLimits: boolean;

  actionName: string;
  actionButtonActive: boolean;
  actionButtonText: string;
  actionButtonOnClick: () => void;

  // TX waiting
  waitingForUserApproval: boolean;
  waitingForTxConfirmation: boolean;
}

const TOP_DISTANCE_REM = 0.5;

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  collateralInteractionPanel: {
    position: "relative",
    width: "30rem",
    maxWidth: "100%",
    backgroundColor: "#F9FAFB",
  },
  panelHeader: {
    width: "100%",
    color: "#141E27",
    display: "flex",
    flexDirection: "row",
    justifyContent: "center",
    alignItems: "center",
    borderBottom: "1px solid rgba(0, 0, 0, 0.05)",

    padding: `${TOP_DISTANCE_REM * 2}rem`,

    // Mobile
    [theme.breakpoints.down("sm")]: {
      // padding: "1.0rem",
      // display: "flex",
      // flexDirection: "row",
      // alignItems: "center",
    },
  },
  title: {
    fontWeight: "bold",
  },
  tokenIcon: {
    marginRight: "0.75rem",
    width: "2.5rem",
    height: "2.5rem",
    position: "fixed",
    left: 0,
  },
  upperRightCorner: {
    position: "absolute",
    right: `${TOP_DISTANCE_REM / 2}rem`,
    top: `${TOP_DISTANCE_REM / 2}rem`,
    // Mobile
    [theme.breakpoints.down("sm")]: {
      right: `${TOP_DISTANCE_REM / 8}rem`,
      top: `${TOP_DISTANCE_REM / 8}rem`,
    },
  },
  upperLeftCorner: {
    position: "absolute",
    left: `${TOP_DISTANCE_REM}rem`,
    top: `${TOP_DISTANCE_REM}rem`,
  },

  messageContainer: {
    paddingTop: "2rem",
    paddingBottom: "1rem",
    paddingLeft: "1.5rem",
    paddingRight: "1.5rem",
    textAlign: "center",

    color: GRAYISH_TEXT,
  },

  tabBody: {
    paddingTop: "2rem",
    paddingBottom: "1rem",
    paddingLeft: "1.5rem",
    paddingRight: "1.5rem",
    backgroundColor: "#ffffff",
  },
}));

export const CollateralModal = React.memo<IProps>((props) => {
  const classes = useStyles();
  const {
    open,
    closeModal,
    actionAssetSymbol,
    actionAssetName,

    showPredictedBorrowLimits,
    currentBorrowLimit,
    usedBorrowLimitPercentage,
    usedBorrowLimitPercentageAfterChange,
    borrowLimitAfterChange,
    actionName,
    actionButtonActive,
    actionButtonText,
    actionButtonOnClick,

    waitingForTxConfirmation,
    waitingForUserApproval,
  } = props;

  if (waitingForTxConfirmation || waitingForUserApproval) {
    const txWaitingStage: TTxWaitingStage = waitingForTxConfirmation
      ? "CONFIRMATION"
      : "USER_APPROVAL";

    return (
      <TxWaitingModal
        open={open}
        closeModal={closeModal}
        txWaitingStage={txWaitingStage}
      />
    );
  }

  return (
    <Modal open={open} className={classes.modal} onBackdropClick={closeModal}>
      <Fade in={open}>
        <div className={classes.collateralInteractionPanel}>
          {/* Quick-n-Dirty x button */}
          <div className={classes.upperRightCorner}>
            <IconButton onClick={closeModal}>
              <CloseIcon />
            </IconButton>
          </div>

          {/* Quick-n-Dirty Market icon */}
          <TokenIcon
            symbol={actionAssetSymbol}
            extraClasses={[classes.tokenIcon, classes.upperLeftCorner]}
          />

          {/* Header */}
          <div className={classes.panelHeader}>
            <Typography variant={"h6"} className={classes.title}>
              {actionName} as collateral
            </Typography>
          </div>

          <div className={classes.messageContainer}>
            <Typography>
              Each asset used as collateral increases your borrowing limit.{" "}
              <br />
              Be careful, this can subject the asset to being seized in
              liquidation.
              {/*Learn more.*/}
            </Typography>
          </div>

          {/* Tab Body */}
          <div className={classes.tabBody}>
            {/* Borrow limit rates */}
            <BorrowLimitRates
              limitOrBalance={"Limit"}
              borrowLimitUsd={currentBorrowLimit || 0}
              borrowLimitUsedPercentage={usedBorrowLimitPercentage || 0}
              predictedBorrowLimitPercentage={
                usedBorrowLimitPercentageAfterChange || 0
              }
              predictedBorrowLimitUsd={borrowLimitAfterChange || 0}
              displayPredictedValues={showPredictedBorrowLimits}
            />

            <br />
            <br />

            {/* Action button */}
            <ModalActionButton
              onClick={actionButtonOnClick}
              text={actionButtonText}
              disabled={!actionButtonActive}
            />
          </div>
        </div>
      </Fade>
    </Modal>
  );
});
