import React, {
  ReactNode,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from "react";
import {
  IMarketModalCommonStatePayload,
  IMarketModalPerTabStatePayload,
  IMarketModalStatePayload,
  MarketInteractionModal,
  TZeroOrOne,
} from "./base/MarketInteractionModal";
import { Button, Input, Typography } from "@material-ui/core";
import { observer } from "mobx-react";
import { BigNumber, ethers } from "ethers";
import {
  useCalculateChangeInBorrowLimits,
  useMarketInteractionActiveTabState,
  useMarketInteractionModalBase,
  useMarketInteractionModalTabsState,
  useOnEnableClicked,
} from "./marketInteractionModalHooks";
import { MainModalMessage } from "./base/mainModalMessage/MainModalMessage";
import {
  useTxWaitingLogicExplicit,
  useTxWaitingStateExplicit,
} from "../txWaitingModal/txWaitingHooks";
import { TModalFooterMessagePayload } from "./base/ModalFooter";
import { useSystemStore } from "../../../store/storeHooks";

interface IProps {
  open: boolean;
  closeModal: () => void;
}

export const SupplyModal = observer<React.FunctionComponent<IProps>>(
  (props) => {
    const { open, closeModal } = props;

    const systemStore = useSystemStore();

    const {
      lendingNetworksStore,
      cTokenStore,
      modalsStore,
      accountStore,
      assetBaseAddress,
      maximillionAddress,
      activeLNStore,
      isNativeCoin,
      cToken,
      isCollaterable,
      collateralPotentialInUSD,

      walletBalance,
      hasWalletBalance,
      needToApprove,

      distributionTokenSymbol,
      supplyTokenAddress,
      supplyTokenSymbol,
      supplyTokenName,
      priceCalculationDecimals,
      pricePerUnitUsd,
      // currentBorrowLimitUsd,
      collateralFactorInUnits,
      usedBorrowPercentage,
      totalAccountBorrowBalanceUsd,
      distributionAssetApy,
      borrowApy,
      supplyApy,

      hasAvailableLiquidity,
      liquidityInAssetUnits,
    } = useMarketInteractionModalBase();

    const {
      activeTabIndex,
      setActiveTabIndex,
    } = useMarketInteractionModalTabsState();

    const {
      activeTxHash,
      setActiveTxHash,
      waitingForUserApproval,
      waitingForTxConfirmation,
      setWaitingForUserApproval,
      setWaitingForTxConfirmation,
    } = useTxWaitingStateExplicit();

    const [isSupply, isWithdraw] = useMarketInteractionActiveTabState(
      activeTabIndex
    );

    const onEnableClicked = useOnEnableClicked(
      cToken,
      assetBaseAddress,
      closeModal,
      setActiveTxHash,
      setWaitingForUserApproval,
      setWaitingForTxConfirmation
    );

    // TODO : C.F.H : Follow the 'repay max' example
    const [isAskingToWithdrawAll, setIsAskingToWithdrawAll] = useState(false);
    const [supplyInputValue, setSupplyInputValue] = useState(0);
    const [withdrawInputValue, setWithdrawInputValue] = useState(0);

    useEffect(() => {
      let newToken = cToken;
      setIsAskingToWithdrawAll(false);
    }, [cToken]);

    const clearStateBeforeClose = useCallback(() => {
      setSupplyInputValue(0);
      setWithdrawInputValue(0);
      closeModal();
    }, [closeModal]);

    // Constants
    const tab0Title = "Supply";
    const tab1Title = "Withdraw";

    // TODO : Separate inputs for each usage / make a component
    const inputComponent = useMemo(() => {
      return (
        <div
          style={{
            width: "100%",
            padding: "2.5rem",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Input
            style={{ textAlign: "center" }}
            inputProps={{ style: { textAlign: "center", appearance: "none" } }}
            type={"number"}
            title={"Input"}
            name={"Input"}
            value={isSupply ? supplyInputValue : withdrawInputValue}
            onChange={(e) =>
              isSupply
                ? setSupplyInputValue(sanitizeNumericInput(e.target.value))
                : setWithdrawInputValue(sanitizeNumericInput(e.target.value))
            }
          />
        </div>
      );
    }, [isSupply, supplyInputValue, withdrawInputValue]);

    // TODO : CRITICAL : Add test for 'some' approval (not unlimitted) as well
    const hasApprovedERC20 = cTokenStore?.hasAnyAllowance;
    const currentSupplyBalanceInUnits = cTokenStore?.accountSupplyInUnits || 0;
    const currentSupplyBalanceInUsd = cTokenStore?.accountSupplyBalanceUsd || 0;
    const currentSupplyBalanceInOTokens =
      cTokenStore?.accountSupplyInOTokensRaw || BigNumber.from(0);
    const hasSupplyBalance = currentSupplyBalanceInUnits > 0;
    const hasBorrowBalance = totalAccountBorrowBalanceUsd > 0;

    const onAskToWithdrawAllToggleClicked = useCallback(() => {
      console.log(`set 'asking to withdraw all' to ${!isAskingToWithdrawAll}'`);
      setIsAskingToWithdrawAll(!isAskingToWithdrawAll);
    }, [isAskingToWithdrawAll]);

    const createSupplyTx = () =>
      accountStore.supplyToMarket(
        cToken,
        ethers.utils.parseUnits(
          supplyInputValue.toString(),
          cTokenStore?.underlyingDecimals || -1
        ),
        isNativeCoin,
        maximillionAddress
      );

    const createWithdrwaTx = () =>
      accountStore.withdrawFromMarket(
        cToken,
        ethers.utils.parseUnits(
          withdrawInputValue.toString(),
          cTokenStore?.underlyingDecimals || -1
        ),
        isNativeCoin,
        maximillionAddress
      );

    const createWithdrawAllTx = useCallback(
      () =>
        // DEV_NOTE : In order to draw all, we draw by specific amount of all of the oTokens
        accountStore.withdrawFromMarketByOTokens(
          cToken,
          currentSupplyBalanceInOTokens,
          isNativeCoin,
          maximillionAddress
        ),
      [
        accountStore,
        cToken,
        currentSupplyBalanceInOTokens,
        isNativeCoin,
        maximillionAddress,
      ]
    );

    console.log(
      `Underlying decimals (${supplyTokenSymbol}) : ${cTokenStore?.underlyingDecimals}`
    );

    console.log(`Input value : ${withdrawInputValue.toString()}`);

    const wrappedSupplyTx = useTxWaitingLogicExplicit(
      createSupplyTx,
      closeModal,
      setActiveTxHash,
      setWaitingForUserApproval,
      setWaitingForTxConfirmation,
      true
    );

    const wrappedWithdrawTx = useTxWaitingLogicExplicit(
      createWithdrwaTx,
      closeModal,
      setActiveTxHash,
      setWaitingForUserApproval,
      setWaitingForTxConfirmation,
      true
    );

    const wrappedWithdrawAllTx = useTxWaitingLogicExplicit(
      createWithdrawAllTx,
      closeModal,
      setActiveTxHash,
      setWaitingForUserApproval,
      setWaitingForTxConfirmation,
      true
    );

    const onSupplyClicked = useCallback(async () => {
      console.log(`Supply button clicked`);

      await wrappedSupplyTx();

      console.log(`Supply is done !`);
    }, [wrappedSupplyTx]);

    const onWithdrawClicked = useCallback(async () => {
      console.log(`Withdraw button clicked`);

      // Withdraw all or just some ?
      if (isAskingToWithdrawAll) {
        await wrappedWithdrawAllTx();
      } else {
        await wrappedWithdrawTx();
      }

      console.log(`Withdraw is done !`);
    }, [isAskingToWithdrawAll, wrappedWithdrawAllTx, wrappedWithdrawTx]);

    const supplyTokenExplorerLink = `${systemStore.explorerBaseAddressUrl}/${supplyTokenAddress}`;

    console.log({ isAskingToWithdrawAll });
    const messageOrInputComponent = useMemo<ReactNode>(() => {
      if (isSupply) {
        if (needToApprove && !hasApprovedERC20) {
          // Message component - needs to approve
          return (
            <MainModalMessage
              tokenSymbol={supplyTokenSymbol}
              message={`Please approve ${supplyTokenSymbol} usage`}
              explorerLink={supplyTokenExplorerLink}
            />
          );
        } else {
          // Input component - how much to supply ?
          return inputComponent;
        }
      } else if (isWithdraw) {
        if (isAskingToWithdrawAll) {
          return (
            <MainModalMessage
              tokenSymbol={supplyTokenSymbol}
              message={`Withdrawing all`}
              subMessage={`~${(+currentSupplyBalanceInUnits.toFixed(
                4
              )).toLocaleString()}`}
              explorerLink={supplyTokenExplorerLink}
            />
          );
        } else {
          // Input component - how much to withdraw ?
          return inputComponent;
        }
      }

      // Safe error
      return <></>;
    }, [
      currentSupplyBalanceInUnits,
      hasApprovedERC20,
      inputComponent,
      isAskingToWithdrawAll,
      isSupply,
      isWithdraw,
      needToApprove,
      supplyTokenExplorerLink,
      supplyTokenSymbol,
    ]);

    const footerMessagePayloads = useMemo<TModalFooterMessagePayload[]>(() => {
      const payloads: TModalFooterMessagePayload[] = [
        {
          title: `${supplyTokenSymbol} Price`,
          tokenAmount: pricePerUnitUsd,
          tokenSymbol: "$",
        },
        {
          title: "Currently Supplying",
          tokenAmount: currentSupplyBalanceInUnits,
          tokenSymbol: supplyTokenSymbol,
        },
        {
          title: "Wallet Balance",
          tokenAmount: walletBalance,
          tokenSymbol: supplyTokenSymbol,
        },
      ];

      return payloads;
    }, [
      currentSupplyBalanceInUnits,
      pricePerUnitUsd,
      supplyTokenSymbol,
      walletBalance,
    ]);

    // const isAskingToWithdrawAllAndCan = isAskingToWithdrawAll &&

    // Must have wallet balance in this asset + Must approve (if needed)
    const canSupply =
      hasWalletBalance && (needToApprove ? hasApprovedERC20 : true);
    const canWithdraw = hasSupplyBalance;

    const borrowLimitNote = !isCollaterable
      ? `Enable ${supplyTokenSymbol} as collateral to affect Borrow limit`
      : undefined;

    // Common payload part
    const commonPayloadPart = useMemo<IMarketModalCommonStatePayload>(() => {
      const commonModalState: IMarketModalCommonStatePayload = {
        // Tabs
        currentTabIndex: activeTabIndex,
        onTabIndexChange: setActiveTabIndex,
        tab0Title,
        tab1Title,

        // Assets
        actionAssetSymbol: supplyTokenSymbol,
        actionAssetName: supplyTokenName,
        distributionAssetSymbol: distributionTokenSymbol,
        assetExplorerLink: supplyTokenExplorerLink,

        // Borrow Limit (optional)
        currentBorrowLimit: collateralPotentialInUSD,
        usedBorrowLimitPercentage: usedBorrowPercentage,

        // Tx Waiting
        explorerName: systemStore.explorerName,
        explorerUrlBaseForTx: systemStore.explorerBaseTxUrl,
        activeTxHash,
        waitingForTxConfirmation,
        waitingForUserApproval,
      };

      return commonModalState;
    }, [
      activeTabIndex,
      activeTxHash,
      collateralPotentialInUSD,
      distributionTokenSymbol,
      setActiveTabIndex,
      supplyTokenExplorerLink,
      supplyTokenName,
      supplyTokenSymbol,
      systemStore.explorerBaseTxUrl,
      systemStore.explorerName,
      usedBorrowPercentage,
      waitingForTxConfirmation,
      waitingForUserApproval,
    ]);

    // Payload for supply
    const marketModalStatePayloadForSupplyTab = useMemo<IMarketModalPerTabStatePayload>(() => {
      const isSupplyInputValueValid = supplyInputValue > 0;
      const isOfferingMoreThanWalletBalance = supplyInputValue > walletBalance;
      const actionButtonTextForSupply = canSupply
        ? "Supply"
        : needToApprove
        ? "Approve"
        : "No Balance";

      const activeButtonActive =
        (isSupplyInputValueValid &&
          canSupply &&
          !isOfferingMoreThanWalletBalance) ||
        (needToApprove && !hasApprovedERC20);
      const actionButtonCbForSupply = canSupply
        ? onSupplyClicked
        : onEnableClicked;

      let reasonForNotBeingAbleToWithdraw = undefined;
      if (!hasWalletBalance) {
        reasonForNotBeingAbleToWithdraw = "No balance to supply";
      } else if (isOfferingMoreThanWalletBalance) {
        reasonForNotBeingAbleToWithdraw = "Exceeding wallet balance";
      } else if (!isSupplyInputValueValid) {
        // No message
        // reasonForNotBeingAbleToWithdraw = "Invalid sum";
      }

      const actionButtonDisabledText = reasonForNotBeingAbleToWithdraw;

      // Calculate the changes to borrow limit (USD and % used)
      const {
        collateralValueOfActionEffect,
        borrowLimitAfterChange,
        usedBorrowLimitPercentageAfterChange,
      } = useCalculateChangeInBorrowLimits(
        collateralPotentialInUSD,
        totalAccountBorrowBalanceUsd,
        supplyInputValue,
        pricePerUnitUsd,
        collateralFactorInUnits,
        priceCalculationDecimals
      );

      const showPredictedBorrowLimits =
        isCollaterable && collateralValueOfActionEffect !== 0;

      // Input action button

      const payload: IMarketModalPerTabStatePayload = {
        // Message or input component
        messageOrInput: messageOrInputComponent,
        inputActionButtonText: "",
        showInputActionButton: false,
        inputActionButtonActive: false,
        onInputActionButtonPressed: undefined,

        // Action Rates
        actionName: "Supply",
        actionApy: supplyApy,
        distributionApy: distributionAssetApy,

        // Borrow Limit (optional)
        // DEV_NOTE : We want to show the effect of both supply and withdraw on the borrow limits
        showBorrowLimit: isWithdraw || canSupply,
        borrowLimitAfterChange: borrowLimitAfterChange,
        usedBorrowLimitPercentageAfterChange: usedBorrowLimitPercentageAfterChange,
        showPredictedBorrowLimits,
        borrowLimitNote,

        // Action button
        actionButtonActive: activeButtonActive,
        actionButtonText: actionButtonTextForSupply,
        actionButtonDisabledText,
        actionButtonOnClick: actionButtonCbForSupply,

        // Footer
        footerMessagesPayloads: footerMessagePayloads,
      };

      return payload;
    }, [
      borrowLimitNote,
      canSupply,
      collateralFactorInUnits,
      collateralPotentialInUSD,
      distributionAssetApy,
      footerMessagePayloads,
      hasApprovedERC20,
      hasWalletBalance,
      isCollaterable,
      isWithdraw,
      messageOrInputComponent,
      needToApprove,
      onEnableClicked,
      onSupplyClicked,
      priceCalculationDecimals,
      pricePerUnitUsd,
      supplyApy,
      supplyInputValue,
      totalAccountBorrowBalanceUsd,
      walletBalance,
    ]);

    // Payload for withdraw
    const marketModalStatePayloadForWithdrawTab = useMemo<IMarketModalPerTabStatePayload>(() => {
      // If requesting to withdraw all, let's make sure the balances will remain ok
      const withdrawUnitsForShortfallCalculationWhenCollaterized = isAskingToWithdrawAll
        ? currentSupplyBalanceInUnits
        : withdrawInputValue;

      const withdrawUnitsForShortfallCalculation = isCollaterable
        ? withdrawUnitsForShortfallCalculationWhenCollaterized
        : 0;

      // Calculate borrow limit (USD and % used) after requested withdraw
      const {
        actionLeadsToShortfall,
        collateralValueOfActionEffect,
        borrowLimitAfterChange,
        usedBorrowLimitPercentageAfterChange,
      } = useCalculateChangeInBorrowLimits(
        collateralPotentialInUSD,
        totalAccountBorrowBalanceUsd,
        withdrawUnitsForShortfallCalculation * -1,
        pricePerUnitUsd,
        collateralFactorInUnits,
        priceCalculationDecimals
      );

      // These params are needed but area calculated a bit differently for when asking to borrow all vs a specific amount
      let reasonForNotBeingAbleToWithdraw = undefined;
      let showPredictedBorrowLimits = false;
      let withdrawButtonActive = false;

      // Is asking to withdraw all ?
      if (isAskingToWithdrawAll) {
        showPredictedBorrowLimits = isCollaterable;

        if (actionLeadsToShortfall) {
          reasonForNotBeingAbleToWithdraw = "Will cause shortfall";
        }

        withdrawButtonActive = !actionLeadsToShortfall;
      } else {
        // Specific amount
        const withdrawInputValid = withdrawInputValue > 0;
        const isRequestingMoreThanSuppliedBalance =
          withdrawInputValue > currentSupplyBalanceInUnits;

        // Make sure the user does not fall into shortfall.
        // DEV_NOTE : This is used to ensure that withdraw will be allowed when there is no borrow +
        //            when this asset is not used as collateral
        const isRequestLegal =
          !actionLeadsToShortfall ||
          !hasBorrowBalance ||
          (hasBorrowBalance && !isCollaterable);

        withdrawButtonActive =
          canWithdraw &&
          withdrawInputValid &&
          !isRequestingMoreThanSuppliedBalance &&
          isRequestLegal;

        if (!hasSupplyBalance) {
          reasonForNotBeingAbleToWithdraw = "No balance to withdraw";
        } else if (isRequestingMoreThanSuppliedBalance) {
          reasonForNotBeingAbleToWithdraw = "Exceeding supplied balance";
        } else if (!withdrawInputValid) {
          // No message
          // reasonForNotBeingAbleToWithdraw = "Invalid sum";
        }

        showPredictedBorrowLimits =
          isCollaterable && collateralValueOfActionEffect !== 0;
      }

      // General params
      const actionButtonCbForWithdraw = onWithdrawClicked;
      const actionButtonTextForWithdraw = "Withdraw";
      const actionButtonDisabledText = reasonForNotBeingAbleToWithdraw;
      const showWithdrawMax = hasSupplyBalance;
      const isWithdrawMaxActive = hasSupplyBalance;
      const withdrawAllToggleButtonText = isAskingToWithdrawAll
        ? "Cancel"
        : "All";

      const payload: IMarketModalPerTabStatePayload = {
        // Message or input component
        messageOrInput: messageOrInputComponent,
        inputActionButtonText: withdrawAllToggleButtonText,
        showInputActionButton: showWithdrawMax,
        inputActionButtonActive: isWithdrawMaxActive,
        onInputActionButtonPressed: onAskToWithdrawAllToggleClicked,

        // Action Rates
        actionName: "Supply",
        actionApy: supplyApy,
        distributionApy: distributionAssetApy,

        // Borrow Limit (optional)
        showBorrowLimit: isWithdraw,
        borrowLimitAfterChange,
        showPredictedBorrowLimits,
        usedBorrowLimitPercentageAfterChange,
        borrowLimitNote,

        // Action button
        actionButtonActive: withdrawButtonActive,
        actionButtonText: actionButtonTextForWithdraw,
        actionButtonDisabledText,
        actionButtonOnClick: actionButtonCbForWithdraw,

        // Footer
        footerMessagesPayloads: footerMessagePayloads,
      };

      return payload;
    }, [
      isAskingToWithdrawAll,
      currentSupplyBalanceInUnits,
      withdrawInputValue,
      isCollaterable,
      collateralPotentialInUSD,
      totalAccountBorrowBalanceUsd,
      pricePerUnitUsd,
      collateralFactorInUnits,
      priceCalculationDecimals,
      onWithdrawClicked,
      hasSupplyBalance,
      messageOrInputComponent,
      onAskToWithdrawAllToggleClicked,
      supplyApy,
      distributionAssetApy,
      isWithdraw,
      borrowLimitNote,
      footerMessagePayloads,
      hasBorrowBalance,
      canWithdraw,
    ]);

    const statePerTabPayload = isSupply
      ? marketModalStatePayloadForSupplyTab
      : marketModalStatePayloadForWithdrawTab;

    const marketModalStatePayload = useMemo<IMarketModalStatePayload>(() => {
      const payload: IMarketModalStatePayload = {
        ...commonPayloadPart,
        ...statePerTabPayload,
      };

      return payload;
    }, [commonPayloadPart, statePerTabPayload]);

    return (
      <MarketInteractionModal
        open={open}
        closeModal={clearStateBeforeClose}
        {...marketModalStatePayload}
      />
    );
  }
);

function sanitizeNumericInput(stringValueOfNumericInput?: string): number {
  let safeString = stringValueOfNumericInput || "0";
  // DEV_NOTE : Barbaric way
  while (safeString.length > 1 && safeString.charAt(0) === "0") {
    console.log(`Starts in 0 : ${safeString}`);
    safeString = safeString.substring(1, safeString.length);
    console.log(`After trim : ${safeString}`);
  }

  const parsedAsFloat = +safeString;
  if (!stringValueOfNumericInput || isNaN(parsedAsFloat)) {
    return 0;
  }

  return parsedAsFloat;
}
