import React from "react";
import {
  Button,
  Fade,
  IconButton,
  makeStyles,
  Modal,
  Typography,
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";
import { TokenIcon } from "../../icons/TokenIcon";
import {
  BACKGROUND_DARK_COLOR,
  BRAND_MAIN_COLOR,
  GRAYISH_TEXT,
  TEXT_ON_DARK,
  TEXT_ON_LIGHT,
} from "../../../theme/themeConstants";
import { ModalActionButton } from "../MarketInteractionModal/base/ModalActionButton";

interface IProps {
  open: boolean;
  closeModal: () => void;

  incentiveTokenAddress: string;
  incentiveTokenName: string;
  incentiveTokenSymbol: string;

  balanceInUnits: number;
  unclaimedInUnits: number;

  canClaim: boolean;
  whyCannotClaim: string;
  onClaim: () => void;

  // Extra native coin balance
  showNativeCoinBalance?: boolean;
  nativeCoinName?: string;
  nativeCoinBalanceInUnits?: number;
}

const TOP_DISTANCE_REM = 0.5;

const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  collateralInteractionPanel: {
    position: "relative",
    width: "30rem",
    maxWidth: "100%",
    // backgroundColor: "#F9FAFB",
    backgroundColor: "#F9FAFB",
  },
  panelHeader: {
    width: "100%",
    color: "#141E27",
    display: "flex",
    flexDirection: "row",
    justifyContent: "center",
    alignItems: "center",
    borderBottom: "1px solid rgba(0, 0, 0, 0.05)",

    padding: `${TOP_DISTANCE_REM * 2}rem`,

    // Mobile
    [theme.breakpoints.down("sm")]: {
      // padding: "1.0rem",
      // display: "flex",
      // flexDirection: "row",
      // alignItems: "center",
    },
  },
  title: {
    fontWeight: "bold",
  },
  tokenIcon: {
    marginRight: "0.75rem",
    width: "2.5rem",
    height: "2.5rem",
    position: "fixed",
    left: 0,
  },
  upperRightCorner: {
    position: "absolute",
    right: `${TOP_DISTANCE_REM / 2}rem`,
    top: `${TOP_DISTANCE_REM / 2}rem`,
    // Mobile
    [theme.breakpoints.down("sm")]: {
      right: `${TOP_DISTANCE_REM / 8}rem`,
      top: `${TOP_DISTANCE_REM / 8}rem`,
    },
  },
  upperLeftCorner: {
    position: "absolute",
    left: `${TOP_DISTANCE_REM}rem`,
    top: `${TOP_DISTANCE_REM}rem`,
  },

  messageContainer: {
    paddingTop: "1rem",
    paddingBottom: "2rem",
    paddingLeft: "1.5rem",
    paddingRight: "1.5rem",
    textAlign: "center",

    color: GRAYISH_TEXT,
    // display: "flex",
    // flexDirection: "row",
  },

  balanceColumn: {
    flex: 1,
    textAlign: "center",
    display: "flex",
    flexDirection: "column",
    // border: `1px solid ${TEXT_ON_LIGHT}`,
  },
  amountTitle: {
    fontWeight: "bold",
    flex: 1,
    paddingBottom: "0.2rem",
  },

  amount: {
    color: TEXT_ON_LIGHT,
  },

  tabBody: {
    // paddingTop: "2rem",
    paddingBottom: "1rem",
    paddingLeft: "1.5rem",
    paddingRight: "1.5rem",
    // backgroundColor: "#ffffff",
  },

  heroToken: {
    height: "4rem",
    width: "4rem",
    marginBottom: "1rem",
  },
}));

export const IncentiveModal = React.memo<IProps>((props) => {
  const classes = useStyles();
  const {
    open,
    closeModal,
    incentiveTokenName,
    incentiveTokenAddress,
    incentiveTokenSymbol,
    balanceInUnits,
    unclaimedInUnits,
    canClaim,
    onClaim,
    whyCannotClaim,
    showNativeCoinBalance,
    nativeCoinName,
    nativeCoinBalanceInUnits,
  } = props;

  return (
    <Modal open={open} className={classes.modal} onBackdropClick={closeModal}>
      <Fade in={open}>
        <div className={classes.collateralInteractionPanel}>
          {/* Quick-n-Dirty x button */}
          <div className={classes.upperRightCorner}>
            <IconButton onClick={closeModal}>
              <CloseIcon />
            </IconButton>
          </div>

          {/* Quick-n-Dirty Market icon */}
          <TokenIcon
            symbol={incentiveTokenSymbol}
            extraClasses={[classes.tokenIcon, classes.upperLeftCorner]}
          />

          {/* Header */}
          <div className={classes.panelHeader}>
            <Typography variant={"h6"} className={classes.title}>
              {incentiveTokenName}
            </Typography>
          </div>

          <div className={classes.messageContainer}>
            <div>
              <TokenIcon
                symbol={incentiveTokenSymbol}
                extraClasses={[classes.heroToken]}
              />
            </div>

            {/* Balances display */}
            <div style={{ display: "flex" }}>
              {/* Incentive balance */}
              <div className={classes.balanceColumn}>
                <Typography className={classes.amountTitle}>
                  {incentiveTokenSymbol} Balance
                </Typography>
                <Typography variant={"body2"} className={classes.amount}>
                  {balanceInUnits.toFixed(4)}
                </Typography>
              </div>

              {/* ? Native coin balance ? */}
              {showNativeCoinBalance && (
                <div className={classes.balanceColumn}>
                  <Typography className={classes.amountTitle}>
                    {nativeCoinName} Balance
                  </Typography>
                  <Typography variant={"body2"} className={classes.amount}>
                    {nativeCoinBalanceInUnits?.toFixed(4)}
                  </Typography>
                </div>
              )}

              {/* Claimable balance */}
              <div className={classes.balanceColumn}>
                <Typography className={classes.amountTitle}>
                  Can claim
                </Typography>
                <Typography variant={"body2"} className={classes.amount}>
                  {unclaimedInUnits.toFixed(4)}
                </Typography>
              </div>
            </div>
          </div>

          {/*/!* Tab Body *!/*/}
          <div className={classes.tabBody}>
            {/* Action button */}
            <ModalActionButton
              onClick={onClaim}
              text={canClaim ? `Claim ${incentiveTokenSymbol}` : whyCannotClaim}
              disabled={!canClaim}
            />
          </div>
        </div>
      </Fade>
    </Modal>
  );
});
